export default class BrowserStorage {

    // ShouldReject - This property determines if the key expect the expirationDate attribute. If yes, and we don't have any, it should reject. Otherwise resolve with the data cached.
    static getKey(key, shouldReject) {
        return new Promise((resolve, reject) => {
            chrome.storage.local.get([key], function (result) {
                if (result[key]) {
                    if (result[key]["expirationDate"]) {
                        const currentDate = new Date();
                        const expirationDate = new Date(result[key]["expirationDate"]);

                        if (currentDate >= expirationDate) {
                            chrome.storage.local.remove([key], function () {
                                reject();
                            });
                        }
                        else {
                            resolve(result[key]);
                        }
                    }
                    else {
                        if (shouldReject === true) {
                            reject();
                        } else {
                            resolve(result[key]);
                        }

                    }
                } else {
                    reject();
                }
            })
        });
    }

    static getAll() {
        return new Promise((resolve, _) => {
            chrome.storage.local.get(null, function(items) {
                resolve(items);
            });
        });
    }

    static setKey(key, value) {
        let object = {};
        object[key] = value;

        return new Promise((resolve, _) => {
            chrome.storage.local.set(object, function () {
                resolve();
            });
        })

    }

    static clearKey(key) {
        return new Promise((resolve, _) => {
            chrome.storage.local.remove(key, function () {
                resolve();
            });
        })
    }

    static async clearMetricsAndLogs() {
        await this.clearKey("usageMetrics");
        await this.clearKey("logs");
    }

    static clearAll() {
        return new Promise((resolve, reject) => {
            chrome.storage.local.clear(function () {
                var error = chrome.runtime.lastError;

                if (error) {
                    reject(error)
                } else {
                    resolve();
                }
            })
        });
    }

    static clearAllExceptWithStartingKey(startingKey) {
        return new Promise((resolve, _) => {    
            BrowserStorage.getAll().then((items) => {

                const filteredKeys = Object.keys(items).filter( (key) => (key.startsWith(startingKey) === false && key.startsWith("PROTECTED") === false));
                let result = filteredKeys.map(async (key) => { 
                    BrowserStorage.clearKey(key) 
                });

                Promise.all(result).then(() => {
                    resolve();
                });
                
            });
        });
    }

    static clearAllWithPrefix(startingKey) {
        return new Promise((resolve, _) => {    
            BrowserStorage.getAll().then((items) => {

                const filteredKeys = Object.keys(items).filter( (key) => (key.startsWith(startingKey) === true));
                let result = filteredKeys.map(async (key) => { 
                    BrowserStorage.clearKey(key) 
                });

                Promise.all(result).then(() => {
                    resolve();
                });
                
            });
        });
    }
    
}