export class URLHelper {
    static setup() {
        if (window === window.top) {
            const handleEvent = (event) => {
                if (event.data === "GET_URL") {
                    const message = {
                        type: "MAIN_URL",
                        hostname: window.location.href
                    };

                    event.source.postMessage(JSON.stringify(message), "*");
                }
            }
        
            window.addEventListener('message', handleEvent, true);
        }
    }

    static getCurrentUrl = () => {
        return new Promise((resolve, _) => {
            setTimeout(() => {
                resolve(undefined);
            }, 5000);
    
            if (window === window.top) {
                resolve(window.location.href);
            } else {
                try {
                    const possibleUrl = document.referrer;
    
                    if (possibleUrl === "") {
                        throw new Error("Undefined");
                    }
    
                    resolve(possibleUrl);
                } catch (e) {
                    // Couldn't get URL through referrer, need to ask for it
                    // Adding timeout as listener isn't immediately available
                    setTimeout(() => {
                        window.addEventListener('message', function(m) {
                            try {
                                const message = JSON.parse(m.data);
                                switch (message.type) {
                                    case 'MAIN_URL':
                                        resolve(message.hostname);
                                        break;
                                    default:
                                        resolve(null);
                                        break;
                                }
                            } catch (e) {
                                resolve(null);
                            }
                        }, false);

                        window.top.postMessage("GET_URL", "*");
                    }, 1000);
                }
            }
        })
    }

}