import { SAAuth } from "../Auth/SAAuth";

import { Logger } from './Logger';

import { Messages, Response, ResponseValues } from '../../shared';

chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
    processMessage(request, sendResponse);
    return true;
});

function processMessage(request, sendResponse) {
    const messages = {
        [Messages.Logger.GetLogs]: getLogs,
        [Messages.Logger.DeleteLogs]: deleteLogs,
        [Messages.Logger.DeleteAllLogs]: deleteAllLogs,
        [Messages.Logger.GetLoggingPreferences]: getLoggingPreferences,
        [Messages.Logger.UpdatePreferences]: updateLoggingPreferences,
        [Messages.Logger.GetUsageMetrics]: getUsageMetrics 
    }

    async function getLogs() {
        try {
            const isLoggedIn = await SAAuth.isLoggedIn();
            const logs = await Logger.getLogs(isLoggedIn);
            return new Response(ResponseValues.Success, { logs });
        } catch(e) {
            return new Response(ResponseValues.Error, { error: e});
        }
    }

    async function deleteLogs(request) {
        try {
            const isLoggedIn = await SAAuth.isLoggedIn();
            await Logger.deleteLogs(request.parameters.logList, isLoggedIn);
            return new Response(ResponseValues.Success);
        } catch(e) {
            return new Response(ResponseValues.Error, { error: e });
        }
    }

    async function deleteAllLogs() {
        try {
            const isLoggedIn = await SAAuth.isLoggedIn();
            await Logger.deleteAllLogs(isLoggedIn);
            return new Response(ResponseValues.Success);
        } catch(e) {
            return new Response(ResponseValues.Error, { error: e });
        }
    }

    async function getLoggingPreferences() {
        try {
            const isLoggedIn = await SAAuth.isLoggedIn();
            const preferences = await Logger.getPreferences(isLoggedIn);
            return new Response(ResponseValues.Success, { preferences });
        } catch(e) {
            return new Response(ResponseValues.Error, { error: e });
        }
    }

    async function updateLoggingPreferences(request) {
        try {
            const isLoggedIn = await SAAuth.isLoggedIn();
            await Logger.setPreferences(isLoggedIn, request.parameters.logging);
            return new Response(ResponseValues.Success);
        } catch(e) {
            return new Response(ResponseValues.Error, { error: e });
        }
    }

    async function getUsageMetrics() {
        try {
            const isLoggedIn = await SAAuth.isLoggedIn();
            const metrics = await Logger.getUsageMetrics(isLoggedIn);
            return new Response(ResponseValues.Success, { metrics });
        } catch(e) {
            return new Response(ResponseValues.Error, { error: e });
        }
    }

    if (messages[request.action]) {
        messages[request.action](request)
        .then((result) => {
            sendResponse(result);
        })
        .catch((error) => {
            sendResponse(error);
        });
    }
}