// Amplify
import awsconfig from '../../Config/aws-exports';
import { Auth, Hub, API } from 'aws-amplify';

import jwt_decode from "jwt-decode";

// External
import {
    CognitoUser,
    CognitoUserSession,
    CognitoIdToken,
    CognitoAccessToken,
    CognitoRefreshToken,
    CognitoUserPool
} from "amazon-cognito-identity-js";

// Owned
import BrowserStorage  from '../BackgroundTools/BrowserStorage';
import { Tools } from '../../Tools/Tools';
import { TokenStorage } from '../../Tools/TokenStorage';
import { KeyManager } from '../KeyManager/KeyManager';
import NativeMessaging from '../../shared/NativeMessaging';
import { SARefreshable } from '../SARefreshable/SARefreshable'
import { Logger } from '../Logger/Logger';

import './AuthMessage';

// Auth events
const listener = (data) => {
    switch (data.payload.event) {
        case 'signIn':
            Tools.debugLog("SignIn Auth event: ", data);
            break;
        case 'signOut':
            Tools.debugLog("SignOut Auth event: ", data);
            SAAuth.performLogout();
            break;
        case 'tokenRefresh':
            Tools.debugLog("TokenRefresh Auth event: ", data);
            break;
        case 'tokenRefresh_failure':
            Tools.debugLog("TokenRefreshFailure Auth event: ");
            Tools.debugLog(data, true);

            switch (data.payload.data.code) {
                case 'NetworkError':
                    Tools.debugLog("Network error refreshing token.");
                    break;
                case 'NotAuthorizedException':
                    Tools.debugLog("Tokens have expired.");
                    SAAuth.logoutUser();
                    break;
                case 'TooManyRequestsException':
                    Tools.debugLog("Too many token refresh requests. Probably don't need to refresh this much, do we?");
                    break;
                default:
                    if (window.navigator.onLine) {
                        SAAuth.logoutUser();
                    }
                    break;
            }
            break;
        default:
            Tools.debugLog("Default Auth event: ", data);
    }
}

Hub.listen('auth', listener);

let authPort;

chrome.runtime.onConnect.addListener(function (port) {
    if (port.name === "optionsAuthListener") {
        authPort = port;
    }
});

export class SAAuth {

    static isTokenExpired(token) {
        const decodedToken = jwt_decode(token);

        const expirationTime = decodedToken.exp;
        const now = Math.round(Date.now() / 1000);

        return now >= expirationTime;
    }

    static async isLoggedIn() {
        try {
            await Auth.currentAuthenticatedUser();
            return true;
        }
        catch {
            return false;
        }
    }

    static async currentSession() {
        return Auth.currentSession();
    }

    static currentUserInfo() {
        return Auth.currentUserInfo();
    }

    static async checkAuthenticatedUser() {
        try {
            const cognitoUser = await Auth.currentAuthenticatedUser();
            const currentSession = await Auth.currentSession();
            Tools.debugLog("[checkAuthenticatedUser] Current session: ");
            Tools.debugLog(currentSession, true);

            // Change this to refresh only if needed
            if (this.isTokenExpired(currentSession.getAccessToken().getJwtToken())) {
                Tools.debugLog("Token expired. Refreshing.");
                cognitoUser.refreshSession(currentSession.getRefreshToken(), (err, session) => {
                    if (session) {
                        Tools.debugLog("Refreshed session!");
                        Tools.debugLog("[BACKGROUND] User logged in: " + JSON.stringify(session));
                    } else {
                        Tools.debugLog("Error refreshing session.");
                        Tools.debugLog("[BACKGROUND] Check Authenticated User without session");
                        Tools.debugLog(err, true);
                    }
                });
            }

            return;
        } catch (e) {
            Tools.debugLog("[BACKGROUND] User not logged in.");
            Tools.debugLog(e, true);
            throw new Error(e);
        }
    }

    static async currentAuthenticatedUser() {
        try {
            await this.checkAuthenticatedUser();
            const user = await Auth.currentAuthenticatedUser();
            return user;
        } catch(e) {
            throw new Error(e);
        }
    }

    static logoutUser() {
        Tools.debugLog("[BACKGROUND] Logout user called");

        return new Promise((resolve, _) => {
            Auth.signOut()
                .then(() => {
                    this.performLogout()
                    resolve();
                })
                .catch(async (e) => {
                    Tools.debugLog("Couldn't Logout User correctly. But will remove session. Error:");
                    Tools.debugLog(e, true);
                    await this.performLogout();
                    resolve();
                });
        });
    }

    static performLogin() {
        try {
            this.sendAuthMessage();
        } catch (e) {
            Tools.debugLog("sendAuthMessage error");
            Tools.debugLog(e, true);
            // Options not listening
        }
    }

    static async performLogout() {
        await Logger.clearMetricsAndLogs();
        this.sendAuthMessage();
    }

    static sendAuthMessage() {
        if (authPort === undefined) {
            return
        }

        authPort.postMessage({
            type: "VALIDATE_AUTH"
        });

    }

    static async authenticateUser(credentials) {
        return new Promise((resolve, reject) => {
            Auth.signIn(credentials.username, credentials.password)
                .then(async (user) => {
                    await BrowserStorage.clearAllExceptWithStartingKey("Cognito");

                    if (user.challengeName === 'NEW_PASSWORD_REQUIRED') {
                        chrome.tabs.create({ 'url': 'https://account.super-agent.com/' });
                        reject('background.reset_password');
                    }

                    const keys = await KeyManager.getUserKeys();
                    
                    if (keys.edek) {
                        KeyManager.generateDEK(credentials.password)
                            .then((result) => {
                                this.performLogin();
                                resolve(user);
                            })
                            .catch((e) => {
                                // If there is an error here decryption failed, which means user has changed password
                                // Need to generate keys again
                                KeyManager.setMasterPassword(credentials.password)
                                    .then((result) => {
                                        this.performLogin();
                                        resolve(user);
                                    });
                            })
                    } else {
                        KeyManager.setMasterPassword(credentials.password)
                            .then((result) => {
                                this.performLogin();
                                resolve(user);
                            });
                    }
                })
                .catch(async (e) => {
                    try {
                        await this.logoutUser();
                    } catch (e) {
                        Tools.debugLog("Error logging user out: ");
                        Tools.debugLog(e, true);
                    }
                    
                    switch (e.code) {
                        case 'NotAuthorizedException':
                        case 'UserNotFoundException':
                            reject('background.wrong_credentials');
                            break;
                        default:
                            reject('background.unknown_error');
                            break;

                    }

                });
        });
    }

    static async changePassword(oldPassword, newPassword) {
        return new Promise(async (resolve, reject) => {
            try {
                const user = await Auth.currentAuthenticatedUser();
                await Auth.changePassword(user, oldPassword, newPassword);
                resolve();
            } catch (e) {
                Tools.debugLog("Error changing password");
                Tools.debugLog(e, true);
                reject(e);
            }
        });
    }

    static async requestRefreshIfNeeded(hasSomeUpdate) {
        if (hasSomeUpdate === false) {
            return
        }

        await SARefreshable.refreshUserPreferences();
        await SARefreshable.refreshCurrentLanguage();
        await SARefreshable.refreshWhitelist();
    }

    // Why do we have this here? There's too much coupling to the native app
    static async checkAppSession() {
        try {
            const appAuthKeys = await NativeMessaging.sendNativeMessageIfPossibleWithResponse("AuthKeys");
            
            if (appAuthKeys === undefined) {
                return;
            }
            Tools.debugLog("[checkAppSession] Got app auth keys: ");
            Tools.debugLog(appAuthKeys, true);

            this.requestRefreshIfNeeded(appAuthKeys.hasSomeUpdate);
            
            try {
                await KeyManager.updateDEK(appAuthKeys.dek);
            }
            catch (e) {
                Tools.debugLog("Couldn't update DEK");
            }

            // If doesn't have tokens and last sign in was made using the app -> Logout
            if (this.shouldPerformLogout(appAuthKeys)) {
                Tools.debugLog("Logging out SAAuth checkAppSession()");
                await this.logoutUser();
            }

            let extensionSession = undefined;
            let username = undefined;

            try {
                await this.checkAuthenticatedUser();
                const userSession = await Auth.currentSession();

                Tools.debugLog("[checkAppSession] User Session: ");
                Tools.debugLog(userSession, true);

                username = userSession.getIdToken().payload['cognito:username'];
                Tools.debugLog("[checkAppSession] Username: ");
                Tools.debugLog(username);
            } catch (e) {
                Tools.debugLog("No Current Session. Will continue execution");
            }

            // The extension has a session, but if it's different from the app, build session using app tokens.
            if ((extensionSession && username !== appAuthKeys.username)) {
                // || (extensionSession === undefined && this.shouldPerformSignIn(appAuthKeys))) {
                Tools.debugLog("Session in Web Extension is different from App. Build from App Session");
                
                try {
                    await this.logoutUser();
                } catch(e) {
                    Tools.debugLog("Error Performing Logout. But don't worry, we can ignore and continue execution");
                }
                
                await this.buildUserSessionFromAppSession();
            }

        } catch (e) {
            Tools.debugLog("Checking App Session error:" + e);
        }

    }

    static async deleteUser() {
        return new Promise(async (resolve, reject) => {
            // Need to delete logs remotely and update log cache as well.
            const apiName = 'auth';
            const path = '/auth';

            const myInit = {
                headers: {},
            };

            try {
                await API.del(apiName, path, myInit);
                resolve();
            } catch (e) {
                reject();
            }
        });
    }

    static async buildUserSessionFromAppSession() {
        try {
            Tools.debugLog("buildUserSessionFromAppSession");

            const appAuthKeys = await NativeMessaging.sendNativeMessageIfPossibleWithResponse("AuthKeys");

            if (this.shouldPerformLogout(appAuthKeys)) {
                await this.logoutUser();
                return
            }

            if (this.shouldPerformSignIn(appAuthKeys)) {
                return await this.buildUserSession(
                    appAuthKeys.idToken,
                    appAuthKeys.accessToken,
                    appAuthKeys.refreshToken,
                    appAuthKeys.username
                );
            }

        } catch (e) {
            Tools.debugLog("buildUserSessionFromAppSession with error: " + e);
        }

    }

    static shouldPerformLogout(appAuthKeys) {
        if (appAuthKeys.username === undefined && appAuthKeys.didSignInThroughApp === true) {
            return true;
        }

        return false;
    }

    static shouldPerformSignIn(appAuthKeys) {
        if (appAuthKeys.username !== undefined &&
            appAuthKeys.idToken !== undefined &&
            appAuthKeys.accessToken !== undefined &&
            appAuthKeys.refreshToken !== undefined) {
            return true;
        }

        return false;
    }

    static async buildUserSessionFromSession(session) {
        return await this.buildUserSession(
            session.idToken.jwtToken,
            session.accessToken.jwtToken,
            session.refreshToken.token,
            session.idToken.payload['cognito:username']
        );

    }

    static async buildUserSession(idTokenString, accessTokenString, refreshTokenString, username) {
        try {
            let idToken = new CognitoIdToken({
                IdToken: idTokenString
            });
            let accessToken = new CognitoAccessToken({
                AccessToken: accessTokenString
            });
            let refreshToken = new CognitoRefreshToken({
                RefreshToken: refreshTokenString
            });

            const sessionData = {
                IdToken: idToken,
                AccessToken: accessToken,
                RefreshToken: refreshToken,
            }

            const user = new CognitoUser({
                Username: username,
                Pool: new CognitoUserPool({
                    UserPoolId: awsconfig.aws_user_pools_id,
                    ClientId: awsconfig.aws_user_pools_web_client_id
                }),
                Storage: TokenStorage
            });

            let userSession = new CognitoUserSession(sessionData);

            Tools.debugLog("Building user session with: ");
            Tools.debugLog(userSession, true);
            if (userSession.isValid() === false) {
                try {
                    await this.checkAuthenticatedUser();
                } catch(e) {

                }
            }

            user.setSignInUserSession(userSession);

            this.sendAuthMessage();
        } catch (e) {
            Tools.debugLog("[BACKGROUND] - buildUserSession - Error building user session: " + e);
        }
    }
}