import { Analytics } from 'aws-amplify';
// import { track as amplitudeTrack } from '@amplitude/analytics-browser';
import awsconfig from '../../Config/aws-exports';
import BrowserStorage from '../BackgroundTools/BrowserStorage';
import { StorageKeys } from '../BackgroundTools/StorageKeys';
import { v4 as uuidv4 } from 'uuid';
import amplitudeConfig from '../../Config/amplitude-config';
import { storageLock } from '../../Tools/StorageLock';

const amplitudeTrack = () => {}

const provider = "AWSKinesisFirehose";

const events = {
    EXECUTION: "execution",
    INSTALL: "install",
    UPDATE: "update",
    SUBSCRIPTION_TAB_SHOWN: "shownSubscriptionTab",
    NATIVE_APP_OPEN: "nativeAppOpen",
    POPUP_OPEN: "popupOpen",
    SAVE_USER_PREFERENCES: "saveUserPreferences",
    PAGE_OPEN: "pageOpen",
}

export class SAAnalytics {
    static async getUserID() {
        const empty = () => {
            return "00000000-0000-0000-0000-000000000000";
        }

        const generateUuid = async () => {
            try {
                const userId = uuidv4();
                await BrowserStorage.setKey(StorageKeys.UserID, userId);
                return userId;
            } catch(e) {
                return empty();
            }
        }

        return storageLock.acquire(StorageKeys.UserID, async () => {
            try {
                const userId = await BrowserStorage.getKey(StorageKeys.UserID, false);
                if (userId == null || userId === empty()) {
                    return generateUuid();
                }
                return userId;
            } catch(e) {
                return generateUuid();
            }
        });

    }

    static async recordEvent(eventName, immediate, eventData = {}) {
        const userId = await this.getUserID();
        const version = chrome.runtime.getManifest().version;
        Analytics.record({
            data: {
                userId: userId,
                name: eventName,
                version: version
            },
            immediate: immediate,
            streamName: awsconfig.kinesis_stream_name
        }, provider);

        if (amplitudeConfig.enabled()) {
            amplitudeTrack(eventName, eventData)
        }
    }

    static recordInstall() {
        this.recordEvent(events.INSTALL, false);
    }

    static recordUpdate() {
        this.recordEvent(events.UPDATE, false);
    }

    static async recordExecution() {
        this.recordEvent(events.EXECUTION, false);
    }

    static async recordSubscriptionTabShown() {
        this.recordEvent(events.SUBSCRIPTION_TAB_SHOWN, true);
    }

    static async recordNativeAppOpen() {
        this.recordEvent(events.NATIVE_APP_OPEN, true);
    }

    static async recordPopupOpen() {
        this.recordEvent(events.POPUP_OPEN, true);
    }

    static async recordSaveUserPreferences(userPreferences) {
        this.recordEvent(events.SAVE_USER_PREFERENCES, true, userPreferences)
    }

    static async recordPageOpened(pageName) {
        this.recordEvent(events.PAGE_OPEN, true, { page: pageName });
    }
}
