export class AsyncLock {
    constructor() {
        this.queues = new Map();
    }

    async acquire(key, fn) {
        if (!this.queues.has(key)) {
            this.queues.set(key, []);
        }

        const queue = this.queues.get(key);

        return new Promise(async (resolve, reject) => {
            const taskFn = async () => {
                try {
                    const result = await fn();
                    resolve(result);
                } catch (error) {
                    reject(error);
                } finally {
                    queue.shift();
                    if (queue.length > 0) {
                        const nextTask = queue[0];
                        nextTask();
                    } else {
                        this.queues.delete(key);
                    }
                }
            };

            queue.push(taskFn);

            if (queue.length === 1) {
                queue[0]();
            }
        });
    }
}
