//@ts-check
import { Request, Messages } from '../shared';
import { Tools } from '../Tools/Tools';

export class Notifications {
    static async notifyTabFinished(tabId, hostname) {
        try {
            const results = await new Request(Messages.Tab.TabFinished, { tabId, hostname }).sendRequest();
            return results;
        } catch(e) {
            return e;
        }
    }

    static async notifyNoEntitlement(tabId, hostname) {
        try {
            const results = await new Request(Messages.Tab.TabNoEntitlement, { tabId, hostname }).sendRequest();
            return results;
        } catch(e) {
            return e;
        }
    }

    static async notifyContentScriptStarted() {
        try {
            Tools.debugLog("Sending message");
            const results = await new Request(Messages.Generic.ContentScriptStarted).sendRequest();
            Tools.debugLog("Returned from sending message");
            return results;
        } catch(e) {
            Tools.debugLog("Error from sending message: " + e);
            return e;
        }
    }
}