import { Tools } from '../../Tools/Tools';
import { Step } from './Step';
import { CookieItem } from './CookieItem';

const Actions = {
    RejectAll: "REJECT_ALL",
    AcceptNecessary: "ACCEPT_NECESSARY",
    AcceptDefault: "ACCEPT",
    SaveSettings: "SAVE_SETTINGS",
    AutoConsent: "AUTO_CONSENT"
};

const Outcomes = {
    AcceptedDefault: "ACCEPTED_DEFAULT",
    AcceptedNecessary: "ACCEPTED_NECESSARY",
    PreferencesApplied: "PREFERENCES_APPLIED",
    RejectedAll: "REJECTED_ALL"
};

export class Action {
    constructor(action, userPreferences, currentDom, cookieItems) {
        this.action = action;
        this.userPreferences = userPreferences;
        this.currentDom = currentDom;
        this.cookieItems = cookieItems;
        this.clickCount = 0;
        this.finishExecution = false;
        this.outcome = "";
    }

    canRejectAll() {
        const userPreferencesEntries = Object.entries(this.userPreferences);
        const cookieKeys = [
            'advertising',
            'functional',
            'performance',
            'other',
        ]
        const cookieSettings = userPreferencesEntries.filter(([key]) => cookieKeys.includes(key))

        return !cookieSettings.some(([_, p]) => p === true);
    }

    update(stepResults) {
        this.currentDom = stepResults.currentDom || this.currentDom;
        this.cookieItems = stepResults.cookieItems || null;
        this.clickCount += stepResults.clickCount || 0;
        this.finishExecution = stepResults.finishExecution || false;
    }

    async executeAction() {
        const self = this;

        const actions = {
            [Actions.RejectAll]: rejectAll,
            [Actions.AcceptNecessary]: acceptNecessary,
            [Actions.AcceptDefault]: acceptDefault,
            [Actions.SaveSettings]: saveSettings,
            [Actions.AutoConsent]: autoConsent
        }

        async function runActionSteps() {
            for (const s of self.action.steps) {
                const step = new Step(s, self.userPreferences, self.currentDom);
                const stepResults = await step.executeStep();
                self.update(stepResults);
            }
        }

        async function rejectAll() {
            if (self.canRejectAll()) {
                try {
                    await runActionSteps();
                    self.outcome = Outcomes.RejectedAll;
                    self.finishExecution = true;
                } catch(e) {
                    Tools.debugLog("Error in rejectAll.");
                    Tools.debugLog(e, true);
                }
            }
        }

        async function acceptNecessary() {
            try {
                await runActionSteps();
                self.outcome = Outcomes.AcceptedNecessary;
                self.finishExecution = true;
            } catch(e) {
                Tools.debugLog("Error in acceptNecessary.");
                Tools.debugLog(e, true);
            }
        }

        async function acceptDefault() {
            try {
                await runActionSteps();
                self.outcome = Outcomes.AcceptedDefault;
                self.finishExecution = true;
            } catch(e) {
                Tools.debugLog("Error in acceptDefault.");
                Tools.debugLog(e, true);
            }
        }

        async function saveSettings() {
            try {
                await runActionSteps();
                self.outcome = Outcomes.PreferencesApplied;
                self.finishExecution = true;
            } catch(e) {
                Tools.debugLog("Error in saveSettings.");
                Tools.debugLog(e, true);
            }
        }

        async function autoConsent() {
            const startingDom = self.currentDom;
            for (const item of self.cookieItems) {
                try {
                    const cookieItem = new CookieItem(self.action.steps, item, self.userPreferences);
                    const results = await cookieItem.consentCookieItems();
                    self.update(results);
                } catch(e) {
                    Tools.debugLog("Couldn't auto-consent in cookie item");
                    Tools.debugLog(item, true);
                }
            }
            self.currentDom = startingDom;
        }

        async function genericAction() {
            await runActionSteps();
        }

        Tools.debugLog("Running ACTION " + self.action.name);
        if (actions[self.action.name]) {
            await actions[self.action.name]();
            return self;
        } else {
            await genericAction();
            return self;
        }
    }
}
