import { Tools } from "../Tools/Tools";

export default class NativeMessaging {

    static handleNativeMessageResponse = (response) => {
        Tools.debugLog("NativeMessage Response: ");
        Tools.debugLog(response, true);
    }

    static handleNativeMessageError = (error) => {
        Tools.debugLog("NativeMessage Error: ", error);
    }

    static sendHelloMessage = () => {
        this.sendNativeMessageIfPossible("Hello");
    }

    static sendNativeMessageIfPossible = async (message) => {

        let containsPermissions = await this.containsPermissions();

        if (containsPermissions) {
            return this.sendNativeMessage(message);
        } else {
            Tools.debugLog("The extension doesn't contain the NativeMessaging permission");
        }

    }

    static sendNativeMessageIfPossibleWithResponse = async (message) => {

        let containsPermissions = await this.containsPermissions();

        if (containsPermissions) {
            return this.sendNativeMessageWithResponse(message);
        } else {
            Tools.debugLog("The extension doesn't contain the NativeMessaging permission");
        }

    }

    static sendNativeMessageWithResponse = async (message) => {

        Tools.debugLog("Sending NativeMessage: " + message);

        return browser.runtime.sendNativeMessage("com.agent.super.extension.safari", { "message": message });

    }

    static sendNativeMessage = (message) => {

        Tools.debugLog("Sending NativeMessage: " + message);

        var sending = browser.runtime.sendNativeMessage("com.agent.super.extension.safari", { "message": message });
        sending.then(this.handleNativeMessageResponse, this.handleNativeMessageError);

    }

    static containsPermissions = () => {
        return new Promise((resolve, _) => {
            chrome.permissions.contains({ permissions: ['nativeMessaging'] }, (result) => {
                if (result) {
                    resolve(true);
                } else {
                    Tools.debugLog("Doesn't contain permission for NativeMessaging");
                    resolve(false);
                }
            });
        });
    }

}
