import { DomTools } from "./DomTools";
import { Tools } from "../../Tools/Tools";
import { Detector } from '../../Tools/Detector';

const ConsentSteps = {
    checkedClick: "checkedClick",
    dataChecked: "data-checked",
    checkboxMouseClick: "checkboxMouseClick",
    ariaChecked: "ariaChecked",
    checkbox: "checkbox",
    setSliderValue: "setSliderValue",
    sliderClick: "sliderClick",
    objectionButton: "objectionButton",
    select: "select",
    yesNo: "yesNo",
    yesNoFind: "yesNoFind",
    className: "className"
}

export class Consent {
    constructor(item, step, itemCategory, userPreferences) {
        this.item = item;
        this.step = step;
        this.itemCategory = itemCategory;
        this.userPreferences = userPreferences;
        this.clickCount = 0;
    }

    async executeConsent() {
        const self = this;

        const consentSteps = {
            [ConsentSteps.checkedClick]: checkedClick,
            [ConsentSteps.dataChecked]: dataChecked,
            [ConsentSteps.checkboxMouseClick]: checkboxMouseClick,
            [ConsentSteps.ariaChecked]: ariaChecked,
            [ConsentSteps.checkbox]: checkbox,
            [ConsentSteps.setSliderValue]: setSliderValue,
            [ConsentSteps.sliderClick]: sliderClick,
            [ConsentSteps.objectionButton]: objectionButton,
            [ConsentSteps.select]: select,
            [ConsentSteps.yesNo]: yesNo,
            [ConsentSteps.yesNoFind]: yesNoFind,
            [ConsentSteps.className]: className
        }

        async function checkedClick() {
            const itemIsChecked = self.item.getAttribute("checked") === 'checked';

            if (itemIsChecked !== self.userPreferences[self.itemCategory]) {
                self.item.click();
                self.clickCount++;
            }
        }

        async function dataChecked() {
            if (self.item.getAttribute('data-checked') == null) {
                if (self.userPreferences[self.itemCategory]) {
                    self.item.click();
                    self.clickCount++;
                }
            }
            else {
                if (!self.userPreferences[self.itemCategory]) {
                    self.item.click();
                    self.clickCount++;
                }
            }
        }

        async function checkboxMouseClick() {
            if (self.item.checked !== self.userPreferences[self.itemCategory]) {
                self.item.dispatchEvent(new Event('click'));
                self.clickCount++;
            }
        }

        async function ariaChecked() {
            if (self.item.getAttribute('aria-checked') !== self.userPreferences[self.itemCategory].toString()) {
                self.item.click();
                self.clickCount++;
            }
        }

        async function checkbox() {
            if (self.item.checked !== self.userPreferences[self.itemCategory]) {
                self.item.click();
                self.clickCount++;
            }
        }

        async function setSliderValue() {
            self.item[self.step.property] = 0;
            self.clickCount++;
        }

        async function sliderClick() {
            self.item.click();
            self.clickCount++;
        }

        async function objectionButton() {
            if (self.userPreferences[self.temCategory] === false) {
                self.item.click();
                self.clickCount++;
            }
        }

        async function select() {
            if (self.userPreferences[self.itemCategory] === false) {
                self.item.selectedIndex = self.step.noIndex;
            } else {
                self.item.selectedIndex = self.step.yesIndex;
            }
            self.clickCount++;
        }

        async function yesNo() {
            try {
                if (self.userPreferences[self.itemCategory] === false) {
                    const stepDetector = new Detector(self.item, self.step.no.split(','));
                    const noItem = await stepDetector.detectSelector(self.step.timeout ? self.step.timeout : 1000);

                    noItem.click();
                } else {
                    const stepDetector = new Detector(self.item, self.step.yes.split(','));
                    const yesItem = await stepDetector.detectSelector(self.step.timeout ? self.step.timeout : 1000);

                    yesItem.click();
                }
                self.clickCount++;
            } catch (e) {
                Tools.debugLog("Error in yesNo.");
                Tools.debugLog(e, true);
                throw new Error(e);
            }
        }

        async function yesNoFind() {
            try {
                const filter = self.userPreferences[self.itemCategory] ? self.step.filterYes : self.step.filterNo;
                const elements = 
                    await DomTools.findElements(self.item, self.step.selector, self.step.timeout, filter.split(","));
                for (const element of elements) {
                    element.click();
                    self.clickCount++;
                }
            } catch(e) {
                Tools.debugLog("Error in yesNoFind: " );
                Tools.debugLog(e, true);
                throw new Error(e);
            }
        }

        async function className() {
            if (self.userPreferences[self.itemCategory]) {
                if (self.item.className !== self.step.classOn) {
                    self.item.className = self.step.classOn;
                    self.clickCount++;
                }
            } else {
                if (self.item.className !== self.step.classOff) {
                    self.item.className = self.step.classOff;
                    self.clickCount++;
                }
            }
        }

        Tools.debugLog("Running Consent step: ");
        Tools.debugLog(self.step, true);
        if (consentSteps[self.step.consentType]) {
            try {
                await consentSteps[self.step.consentType]();
                return self;
            } catch(e) {
                Tools.debugLog("Error in Consent step: ");
                Tools.debugLog(e);
                throw new Error(e);
            }
        }
    }
}