import { Tools } from '../../Tools/Tools';
import { Detector } from '../../Tools/Detector';
import { Consent } from './Consent';
import { Messages, Request } from '../../shared';

const CookieItemSteps = {
    DecideCategory: "DECIDE_CATEGORY",
    GetElement: "GET_ELEMENT",
    ConsentCategory: "CONSENT_CATEGORY",
    GoToParent: "GO_TO_PARENT",
    GoToChild: "GO_TO_CHILD",
}

export class CookieItem {
    constructor(steps, currentDom, userPreferences) {
        this.steps = steps;
        this.cookieItem = currentDom;
        this.userPreferences = userPreferences;

        this.clickCount = 0;
        this.itemCategory = "";
        this.itemList = [];
    }

    update(consentResults) {
        this.clickCount += consentResults.clickCount || 0; 
    }

    async consentCookieItems() {
        for (const step of this.steps) {
            await this.consentCookieItem(step);
        }
        return this;
    }

    async consentCookieItem(step) {
        const self = this;

        const steps = {
            [CookieItemSteps.DecideCategory]: decideCategory,
            [CookieItemSteps.GetElement]: getElement,
            [CookieItemSteps.ConsentCategory]: consentCategory,
            [CookieItemSteps.GoToParent]: goToParent,
            [CookieItemSteps.GoToChild]: goToChild
        }

        async function decideCategory() {
            const result = await new Request(Messages.Category.GetCategories).sendRequest();
            const filterWords = result.message.payload.categories;

            if (step.property === "innerText") {
                self.itemCategory = Tools.guessCategory(filterWords, self.cookieItem.innerText);
            } else if (step.property === "name") {
                self.itemCategory = Tools.guessCategory(filterWords, self.cookieItem.name);
            } else if (step.property === "for") {
                self.itemCategory = Tools.guessCategory(filterWords, self.cookieItem.getAttribute("for"));
            }
            return;
        }

        async function getElement() {
            try {
                Tools.debugLog("Cookie item: ");
                Tools.debugLog(self.cookieItem, true);
                const elementsDetector = new Detector(self.cookieItem, step.selector.split(","));
                const results = await elementsDetector.detectSelectors(step.timeout ? step.timeout : 1000);
                self.itemList = results;
                return;
            } catch(e) {
                Tools.debugLog("Error in getElement");
                Tools.debugLog(e);
                throw new Error(e);
            }
        }

        async function consentCategory() {
            if (self.itemCategory !== "required") {
                if (self.itemList && self.itemList.length > 0) {
                    for (const item of self.itemList) {
                        const consent = new Consent(item, step, self.itemCategory, self.userPreferences);
                        const consentResults = await consent.executeConsent();
                        self.update(consentResults);
                    }
                } else {
                    const consent = new Consent(self.cookieItem, step, self.itemCategory, self.userPreferences);
                    const consentResults = await consent.executeConsent();
                    self.update(consentResults);
                }
            }
            return;
        }

        async function goToParent() {
            self.cookieItem = self.cookieItem.parentElement;
            return;
        }

        async function goToChild() {
            self.cookieItem = self.cookieItem.children[0];
            return;
        }

        Tools.debugLog("Running for CookieItem " + step.type);
        if (steps[step.type]) {
            await steps[step.type]();
            return;
        } 
    }
}
