import tldjs from 'tldjs';
import { Detector } from './Detector';

import { Logger } from 'aws-amplify';

import awsconfig from '../Config/aws-exports';
const env = awsconfig.aws_user_pools_id === 'eu-west-1_iv6CkI7wU' ? 'dev' : 'prod';

export class Tools {
    static delay(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    static debugLog(log, raw) {
        if (env === 'dev') {
            if (raw) {
                console.log(log);   
            } else {
                console.log(window !== window.top ? "[iFrame]" : "[top]", "[SA-DEBUG] " + log);
            }
        } else {
            const logger = new Logger('[SA_DEBUG]');
            logger.debug(log);
        }
    }

    static stringMatches(stringArray, stringToTest) {
        return new RegExp(stringArray.join("|")).test(stringToTest);
    }

    static guessCategory(filterWords, text) {
        if (this.stringMatches(filterWords["advertising"].split(','), text.toLowerCase().trim())) {
            return "advertising";
        }
        if (this.stringMatches(filterWords["functional"].split(','), text.toLowerCase().trim())) {
            return "functional";
        }
        if (this.stringMatches(filterWords["performance"].split(','), text.toLowerCase().trim())) {
            return "performance";
        }
        if (this.stringMatches(filterWords["nonRequired"].split(','), text.toLowerCase().trim())) {
            return "other";
        }
        if (this.stringMatches(filterWords["required"].split(','), text.toLowerCase().trim())) {
            return "required";
        }

        return "other";
    }

    static findProvider = async (providerDetectors, detectTimeout) => {
        const detector = new Detector(document, providerDetectors);
        try {
            const provider = await detector.detectProvider(detectTimeout);
            return provider;
        } catch(e) {
            return null;
        }
    }

    static findExistingProviders = async (providerDetectors) => {
        const detector = new Detector(document, providerDetectors);
        try {
            const providers = await detector.detectProviders();
            return providers;
        } catch(e) {
            return null;
        }
    }

    static providerExists(root, guess, timeout) {
        return new Promise((resolve, reject) => {
            let timer;
            let el = root.querySelector(guess.detector);

            if (el) {
                clearTimeout(timer);
                resolve(guess);
            } else if (timeout === 0) {
                resolve(null);
            }

            let observer = new MutationObserver((mutationRecords, observer) => {
                const element = root.querySelector(guess.detector);
                if (element) {
                    clearTimeout(timer);
                    observer.disconnect();
                    resolve(guess);
                }
            });

            observer.observe(root.documentElement, {
                childList: true,
                subtree: true
            });

            timer = setTimeout(() => {
                observer.disconnect();
                clearTimeout(timer);
                reject(new Error("Timeout looking for " + guess.detector));
            }, timeout)
        });
    }

    static elementReady(root, selector, timeout) {
        return new Promise((resolve, reject) => {
            let timer;
            let el = root.querySelector(selector);

            if (el) {
                clearTimeout(timer);
                resolve(el);
            }

            let observer = new MutationObserver((mutationRecords, observer) => {
                const element = root.querySelector(selector);
                if (element) {
                    clearTimeout(timer);
                    observer.disconnect();
                    resolve(element);
                }
            });

            observer.observe(root, {
                childList: true,
                subtree: true
            });

            timer = setTimeout(() => {
                observer.disconnect();
                clearTimeout(timer);
                reject(new Error("Timeout looking for " + selector));
            }, timeout);
        });
    }

    static elementsReady(root, selector, timeout) {
        return new Promise((resolve, reject) => {
            let timer;
            let el = root.querySelectorAll(selector);

            if (el.length > 0) {
                clearTimeout(timer);
                resolve(el);
            }

            let observer = new MutationObserver((mutationRecords, observer) => {
                const elements = root.querySelectorAll(selector);
                if (elements.length > 0) {
                    clearTimeout(timer);
                    observer.disconnect();
                    resolve(elements);
                }
            });

            observer.observe(root, {
                childList: true,
                subtree: true
            });

            timer = setTimeout(() => {
                observer.disconnect();
                clearTimeout(timer);
                reject(new Error("Timeout looking for " + selector));
            }, timeout);

        });
    }
    
    static hideElement(selector) {
        document.querySelector(selector).style.opacity = 0;
        document.querySelector(selector).style.display = "none";
    }

    static getProperty(selector, property) {
        return document.querySelector(selector)[property];
    }


    static isHidden(provider) {
        if (provider.ignoreVisibility && provider.ignoreVisibility === true) {
            return false;
        }
        
        const node = document.querySelector(provider.detector);

        if (window.getComputedStyle(node).visibility === "hidden") {
            return true;
        }

        if (node.style.display === "none") {
            return true;
        }

        if (!provider.ignoreOffsetHeight) {
            if (node.offsetHeight === 0) {
                return true;
            }
        }

        return false;
    }

    static hideBackground(userPreferences, provider) {
        if (userPreferences.background) {
            if (provider.hideBackground === false) {
                return
            }

            if (provider.background) {
                for (const b of provider.background.split(",")) {
                    try {
                        this.hideElement(b);
                    }
                    catch (e) {

                    }
                }
            }
            this.hideElement(provider.detector);
        }
    }

    // Detects whether a given cookie is already present
    static detectCookie(provider) {
        if (provider.cookieName) {
            for (const name of provider.cookieName.split(",")) {
                if (document.cookie.includes(name)) {
                    return true;
                }
            }
        } else {
            return false;
        }

        return false;
    }

    static hostnameForURL(url) {
        var hostname = tldjs.parse(url).hostname;
        
        return hostname;
    }

    // This version always returns the hostname presented. However, some cases, like the extension's hostnames, are not supported as regular addresses. Previously we had the validation of `contains("."),` but it is a problem for working in localhost. 
    static rootDomainForURL(url) {
        var hostname = tldjs.parse(url).hostname;
        var domain = tldjs.getDomain(url);

        return domain ? domain : hostname;
    }

    static checkAutoRefresh(executionResults, timeDiffThreshold, hostname) {
        const updateTime = executionResults.updateTime;
        const timeDiffInSeconds = ((new Date()).getTime() - (new Date(updateTime)).getTime()) / 1000;

        if (timeDiffInSeconds < timeDiffThreshold && executionResults.hostname === hostname) {
            Tools.debugLog("PROBABLE AUTO REFRESH DETECTED");
            return true;
        }
    }
    
}