import { RulesEngine } from './RulesEngine/RulesEngine';

// Tools imports
import { Tools } from '../Tools/Tools';

import { Request, Messages } from '../shared';
import { OUTCOMES, RunStatus } from './RunStatus';

export class Run {

    static run = async (actions, tabId, hostname, provider) => {
        const results = await new Request(Messages.UserPreferences.GetUserPreferences).sendRequest();
        const userPreferences = results.message.payload.userPreferences;
    
        if (provider.background) {
            Tools.hideBackground(userPreferences, provider);
        }
        
        const rulesEngine = new RulesEngine(userPreferences, actions);

        return new Promise( (resolve, reject) => {
            setTimeout( () => {
                rulesEngine.execute()
                .then(async (executionResults) => {            
                    executionResults.hostname = hostname;
                    await new Request(Messages.Tab.UpdateExecutionResults, { tabId, executionResults }).sendRequest();
                    resolve();
                })
                .catch((e) => {
                    Tools.debugLog(e, true);
                    reject(e);
                });
            }, provider.delay ? provider.delay : 0);
        });
    }

    static runForURL = async (rootDomain, hostname, tabId, isEligible) => {
        try {
            let provider;
            let actions;
            
            const result = 
                await new Request(Messages.Provider.GetProviderInfo, { provider: rootDomain }).sendRequest();

            provider = result.message.payload.provider;

            if (provider && provider.actions) {
                provider.main = true;
                actions = provider.actions;

                // If user is not eligible, return an error here.
                if (isEligible) {
                    await this.run(actions, tabId, hostname, provider);
                    await new Request(Messages.Entitlement.IncrementCounter, { hostname: hostname }).sendRequest();

                    return new RunStatus(OUTCOMES.foundSomething);
                } else {
                    Tools.debugLog("No entitlement.");
                    return new RunStatus(OUTCOMES.noEntitlement);
                }
            } else {
                return new RunStatus(OUTCOMES.nothingFound);
            }
        } catch(e) {
            Tools.debugLog("Couldn't run for URL.");
            return new RunStatus(OUTCOMES.nothingFound);
        }
    }

    static runForProviders = async (hostname, tabId, detectTimeout, isEligible) => {
            const results = await new Request(Messages.Provider.GetProviderDetectors).sendRequest();
            let providerDetectors = results.message.payload.providerDetectors;
            let provider = await Tools.findProvider(providerDetectors, detectTimeout);
            let executedOnce = false;

            // Find one provider and run for it asap
            if (provider != null) {
                Tools.debugLog("Found provider: " + JSON.stringify(provider));

                await Tools.delay(provider.delay ? provider.delay : 0);
                 
                // CHECK TO SEE IF ELEMENTS ARE HIDDEN
                if (!Tools.isHidden(provider)) {
                    try {
                        if (isEligible) {
                            await this.executeProviderRules(tabId, hostname, provider);
                            executedOnce = true;
                        } else {
                            return new RunStatus(OUTCOMES.noEntitlement);
                        }
                    } catch(e) {
                        Tools.debugLog("Error executing provider rules: " + e);
                    }
                } else {
                    Tools.debugLog("Not running for provider as it's hidden.");
                    // Need to implement a way of checking again during a time period
                    // Banners may start hidden but then show up
                }
                providerDetectors = providerDetectors.filter(p => p.detector !== provider.detector);
            }

            // Find all potentially missing providers without a timeout; DOM should have fully loaded by now
            const missingProviders = await Tools.findExistingProviders(providerDetectors);
            const filteredProviders = missingProviders.filter(p => p != null);
            let executedProviders;

            if (filteredProviders !== null && filteredProviders.length > 0) {
                if (isEligible) {
                    executedProviders = await Promise.all(
                        filteredProviders.map(provider => this.executeProviderRules(tabId, hostname, provider))
                    );
                } else {
                    Tools.debugLog("No entitlement.");
                    return new RunStatus(OUTCOMES.noEntitlement);
                }
            }

            let anyExecution = executedOnce || executedProviders.some(result => result === true)
            
            if (anyExecution) {
                await new Request(Messages.Entitlement.IncrementCounter, { hostname: hostname }).sendRequest();
                return new RunStatus(OUTCOMES.foundSomething);
            } else {
                return new RunStatus(OUTCOMES.nothingFound);
            }
    }

    static executeProviderRules = async (tabId, hostname, providerToRun) => {
        Tools.debugLog("Running for " + JSON.stringify(providerToRun));
        try {
            const results = 
                await new Request(Messages.Provider.GetProviderInfo, { provider: providerToRun.name }).sendRequest();
            Tools.debugLog("Get provider info results: ");
            Tools.debugLog(results, true);
            const provider = results.message.payload.provider;
            let actions = provider.actions;

            await this.run(actions, tabId, hostname, provider);
            return true;
        } catch(e) {
            Tools.debugLog("Error inside run function.");
            return false;
        }
    }
}
