import b64 from 'base64-arraybuffer';

export class Crypto {
    static async pbkdf2Key(password, salt, iterations=100000) {
        const keyMaterial = await crypto.subtle.importKey(
          'raw',
          password,
          'PBKDF2',
          false,
          ['deriveKey', 'deriveBits']);
    
        const key = await crypto.subtle.deriveKey({
          name: 'PBKDF2',
          hash: 'SHA-256',
          salt: salt,
          iterations: iterations
        }, keyMaterial, {
          name: 'AES-GCM',
          length: 256
        }, true, ['encrypt', 'decrypt']);
    
        return key;
    }

    static async generateKEK(password, salt) {
        const KEK = await this.pbkdf2Key(password, salt);

        return KEK;
    }

    static async generateDEK() {
        const DEK = await crypto.subtle.generateKey(
            {
                name: "AES-GCM",
                length: 256
            },
            true,
            ['encrypt', 'decrypt']
        );

        const exportedDEK = await crypto.subtle.exportKey('raw', DEK);
        return exportedDEK;
    }

    static async encrypt(payload, key, iv) {
        if (iv === undefined) {
            iv = new Uint8Array(12);
            iv = crypto.getRandomValues(iv);
        }

        if (typeof(key) !== 'object') {
            key = await crypto.subtle.importKey(
                    'raw', 
                    b64.decode(key),
                    "AES-GCM",
                    false,
                    ["encrypt", "decrypt"]
                );
        };

        const cipher = await crypto.subtle.encrypt(
            {
              name: "AES-GCM",
              iv: iv
            },
            key,
            payload
          );

        return {
            ciphertext: b64.encode(cipher),
            iv: b64.encode(iv)
        }
    }

    static async decrypt(payload, key, iv) {

        if (typeof(key) !== 'object') {
            key = await crypto.subtle.importKey(
                    'raw', 
                    b64.decode(key),
                    "AES-GCM",
                    false,
                    ["encrypt", "decrypt"]
                );
        };

        try {
            const decryptedP = 
                await crypto.subtle.decrypt( {
                        name: "AES-GCM",
                        iv: iv
                    },
                    key,
                    payload
                );
                

            return b64.encode(decryptedP);
        } catch(e) {
            throw new Error(e);
        }
    }
    
}