import { Tools } from './Tools';

export class Detector {

    constructor(dom, detectors) {
        this.dom = dom;
        this.detectors = detectors;
    }

    // Returns the first found provider
    detectProvider(timeoutMs) {
        return Promise.race(
            this.detectors.map(guess => Tools.providerExists(this.dom, guess, timeoutMs))
        );
    }

    // Returns all found providers
    detectProviders() {
        return Promise.all(
            this.detectors.map(guess => Tools.providerExists(this.dom, guess, 0))
        );
    }

    detectSelector(timeoutMs) {
        return Promise.race(
            this.detectors.map(guess => Tools.elementReady(this.dom, guess, timeoutMs))
        );
    }

    detectSelectors(timeoutMs) {
        return Promise.race(
            this.detectors.map(guess => Tools.elementsReady(this.dom, guess, timeoutMs))
        );
    }
}