//@ts-check

export const ExpirationDateOption = {
    PREFERENCES: "preferences",
    LOGS: "logs",
    PROVIDER: "provider",
    PROVIDER_DETECTORS: "provider_detectors",
    RULES: "rules",
    CATEGORIES: "categories",
    USAGE_METRICS: "usageMetrics",
    REPORTED_WEBSITES: "reportedSites",
    EASYLIST_COOKIES: "easyListCookies",
    RC_ID_INSTALLED_AT: "RCIdInstalledAt",
}

export class ExpirationDates {

    static expirationDateInStringFor(option) {
        return ExpirationDates.expirationDateFor(option).toString();
    }

    static expirationDateFor(option, expirationDate = new Date()) {

        let time = this.expirationTimeFor(option);

        expirationDate.setMinutes(expirationDate.getMinutes() + time);

        return expirationDate;

    }

    static expirationTimeFor(option) {
        switch (option) {

            case ExpirationDateOption.PREFERENCES:
                return 5;

            case ExpirationDateOption.PROVIDER:
                return 2;

            case ExpirationDateOption.REPORTED_WEBSITES:
                return 15;

            case ExpirationDateOption.PROVIDER_DETECTORS:
            case ExpirationDateOption.RULES:
            case ExpirationDateOption.CATEGORIES:
                return 180;

            case ExpirationDateOption.EASYLIST_COOKIES:
                return 240;

            case ExpirationDateOption.RC_ID_INSTALLED_AT:
                return 7 * 24 * 60;
            
            default:
                return 0;

        }

    }

}
