import { v4 as uuidv4 } from 'uuid';

export class Comm {
    constructor() {
        this.channel = uuidv4();
        this.script = 
            `function sdte(p){ var e = new CustomEvent("${this.channel}",{detail:p});document.dispatchEvent(e);} var saapiloaded = new CustomEvent('saapiloaded');document.dispatchEvent(saapiloaded);`;
    }

    getChannel() {
        return this.channel;
    }

    injectScript() {
        var existingScript = document.querySelector("script[id='superagent']");
        if (existingScript != null) {
            existingScript.parentNode.removeChild(existingScript);
        }

        var s = document.createElement('script');
        s.setAttribute("id", "superagent");
        s.textContent = this.script;
        document.documentElement.appendChild(s);
    }
}
