import { Theme } from './Theme';
import BrowserHelper from '../../shared/BrowserHelper';
import { UserPreferences } from '../UserPreferences/UserPreferences';

import { Tools } from '../../Tools/Tools';
import BrowserStorage from './BrowserStorage';
import { StorageKeys } from './StorageKeys';
import { SAAnalytics } from '../Analytics/SAAnalytics';
import { Whitelist } from '../Whitelist/Whitelist';
import { isExpired as isExpiredSubscriptionVerificationPopup } from '../../views/Shared/Functions/isEligible';
import { storageLock } from '../../Tools/StorageLock';
import { Messages, Request } from '../../shared';


// window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', function (e) {
//     BackgroundTools.setIcon();
// })

export class BackgroundTools {
    static async setIfDisabledIcon(tab) {
        try {
            if (!tab) {
                return false;
            }

            const hostname = Tools.rootDomainForURL(tab.url);

            if (hostname === undefined) {
                return false;
            }

            const isBlocked = await Whitelist.hostnameIsBlocked(hostname);
            if (!isBlocked) {
                return false
            }

            await this.setDisabledIcon(tab.id);

            return true;
        } catch (err) {
            Tools.debugLog('[ERROR] setIfDisabledIcon')
            Tools.debugLog(err)
            return false
        }
    }

    static async setDisabledIcon(tabId) {
        try {
            const theme = new Theme();
            const isDark = await theme.isDarkTheme();

            await chrome.browserAction.setIcon({
                path: isDark ? "/images/disabled-dark-small.png" : "/images/disabled-light-small.png",
                tabId: tabId,
            });
        }
        catch (err) {
            Tools.debugLog('[ERROR] setDisabledIcon')
            Tools.debugLog(err)
        }
    }

    static async setIcon(tab) {
        try {
            const isDisabled = await this.setIfDisabledIcon(tab);
            if (isDisabled) {
                return;
            }

            const theme = new Theme();
            const isDark = await theme.isDarkTheme();

            await chrome.browserAction.setIcon({
                path: isDark ? "/images/default-dark-small.png" : "/images/default-light-small.png"
            });
        }
        catch (err) {
            Tools.debugLog('[ERROR] setIcon')
            Tools.debugLog(err)
        }
    }

      static async changeBadgeColor (tabId, error) {
        try {
            const tab = await new Promise((resolve) => chrome.tabs.get(tabId, (tab) => resolve(tab)));
            const isDisabled = await this.setIfDisabledIcon(tab);
            if (isDisabled) {
                return;
            }

            const iconPath = await this.getIconPath(error);
            // This icon needs to have 38px to be handled by Chrome
            chrome.browserAction.setIcon({
                path: iconPath,
                tabId: tabId
            });
        } catch (err) {
            Tools.debugLog('[ERROR] changeBadgeColor')
            Tools.debugLog(err)
        }
      }
      
      static async getIconPath(error){
        const theme = new Theme();
        const isDark = await theme.isDarkTheme();

        const themeName = isDark ? 'dark' : 'light';
        const prefix = error ? "default" : "success";

        return `/images/${prefix}-${themeName}-small.png`;
      }

      static async showOnboard() {
        const isMobileSafari = BrowserHelper.isMobileSafari(navigator.userAgent);
      
        if (isMobileSafari === true) {
          return;
        }
      
        const url = chrome.runtime.getURL("onboard.html");
        await chrome.tabs.create({ url });
      }
      
      static async showUpdate() {
        const isMobileSafari = BrowserHelper.isMobileSafari(navigator.userAgent);
        const userPreferences = await UserPreferences.getUserPreferences();
        const version = chrome.runtime.getManifest().version;  

        if (!userPreferences.showUpdateNotifications) {
          Tools.debugLog("Not showing update notification - UserPreferences.showUpdateNotifications is false");
          return;
        }

        if (version === "3.1.3" /*&& !isMobileSafari*/) {
          const url = chrome.runtime.getURL("update.html");
          await chrome.tabs.create({ url });
        }

      }

      static async showSubscribe() {
        let showNotification = false;

        try {
          const result = await BrowserStorage.getKey(StorageKeys.Entitlement.LastReminder);
          const lastOpened = result.when;

          const timeDiffInMs = new Date() - new Date(lastOpened);
          const fortnightInMs = 14 * 24 * 3600 * 1000;
          
          // Debug: 1 minute
          // const fortnightInMs = 60 * 1000; 
          
          Tools.debugLog("Time elapsed since last subscription reminder (in hours): " + timeDiffInMs/1000/3600);
          showNotification = timeDiffInMs >= fortnightInMs;
        } catch(e) {
          // No key to read, never shown
          showNotification = true;
        }
        
        // Force show notification in case it has never been shown
        try {
          const result = await BrowserStorage.getKey(StorageKeys.Entitlement.HaveEverReminded, false);
          const haveShownInThisVersion = result.value;
          if (!haveShownInThisVersion) {
            showNotification = true;
          }
        } catch(e) {
          Tools.debugLog("Error getting haveremindedinthisversion key.");
          showNotification = true;
        }

        if (showNotification) {
          try {
            const url = chrome.runtime.getURL("subscribe.html");
            await chrome.tabs.create({ url });            
            await SAAnalytics.recordSubscriptionTabShown();
            await BrowserStorage.setKey(StorageKeys.Entitlement.HaveEverReminded, {
              value: true
            });

            const now = new Date();
            await BrowserStorage.setKey(StorageKeys.Entitlement.LastReminder, {
              when: now
            });

          } catch(e) {
            Tools.debugLog("Error opening subscribe tab: ");
            Tools.debugLog(e, true);
          }
        }
      }

      static async showSubscriptionVerificationNotification() {
        try {
          return storageLock.acquire(StorageKeys.Entitlement.PopupShownAt, async () => {
            const result = await new Request(Messages.Entitlement.SubscriptionVerificationNotification.GetPopupShownAt, {}).sendRequest();
            const popupShownAt = result.message.payload.popupShownAt;
            if (!popupShownAt || isExpiredSubscriptionVerificationPopup(popupShownAt)) {
              await new Request(Messages.Entitlement.SubscriptionVerificationNotification.ShowPopup, {}).sendRequest();
            }
          })
        } catch (e) {
            Tools.debugLog("Error opening subscription verification tab: ");
            Tools.debugLog(e, true)
        }
      }
      
}
