import { Tools } from '../../Tools/Tools';
import { Errors } from './Errors';

export class DomTools {
    static async findPositionFixed(root, selector, timeout, filterWords) {
        const elements = await Tools.elementReady(root, selector, timeout);
        let fixedElements = [];
        for (const el of elements) {
            let style = window.getComputedStyle(el);
            if (style.position === "fixed") {
                const elText = el.innerText;
                const lowercasedFilterWords = filterWords.map(word => word.toLowerCase());

                if (Tools.stringMatches(lowercasedFilterWords, elText.toLowerCase())) {
                    fixedElements.push(el);
                }
            }
        }

        if (fixedElements[0]) {
            return fixedElements[0];
        } else {
            throw new Error(Errors.NotFound);
        }
    }

    static async findChildren(root, selector, timeout, filterWords) {
        try {
            const element = await Tools.elementReady(root, selector, timeout);
            const elementsToFilter = element.children;

            const lowercasedFilterWords = filterWords.map(word => word.toLowerCase());

            for (const el of elementsToFilter) {
                const buttonText = el.innerText;
                if (Tools.stringMatches(lowercasedFilterWords, buttonText.toLowerCase())) {
                    return el;
                }
            }
        } catch (e) {
            throw new Error(Errors.NotFound);
        }
    }

    static async findElements(root, selector, timeout, filterWords) {
        try {
            const elements = await Tools.elementsReady(root, selector, timeout);
            let elementsFound = [];
            for (const el of elements) {

                const buttonText = el.innerText;
                const lowercasedFilterWords = filterWords.map(word => word.toLowerCase());
                
                Tools.debugLog("Button text: " + buttonText);
                Tools.debugLog("Filter words: " + lowercasedFilterWords);

                if (Tools.stringMatches(lowercasedFilterWords, buttonText.toLowerCase())) {
                    Tools.debugLog("Found element: ");
                    Tools.debugLog(el, true);
                    elementsFound.push(el);
                } else {
                    Tools.debugLog("Couldn't find");
                }
            }
            return elementsFound;
        } catch(e) {
            throw new Error(Errors.NotFound);
        }
    }

    static hideNode(node) {
        try {
            node.style.display = "none";
            node.style.opacity = 0;
        } catch (e) {
            throw new Error(Errors.NotFound);
        }
    }
}