import { KeyManager } from './KeyManager';
import { SAAuth } from '../Auth/SAAuth';

import { Messages, Response, ResponseValues } from '../../shared';

chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
    processMessage(request, sendResponse);
    return true;
});

function processMessage(request, sendResponse) {
    const messages = {
        [Messages.KeyManager.CheckDEK]: checkDEK,
        [Messages.KeyManager.DeleteDEK]: deleteDEK,
        [Messages.KeyManager.GenerateDEK]: generateDEK,
        [Messages.KeyManager.GetUserKeys]: getUserKeys,
        [Messages.KeyManager.SetMasterPassword]: setMasterPassword
    }

    async function checkDEK() {
        try {
            await KeyManager.checkDEK();
            return new Response(ResponseValues.Success);
        } catch(e) {
            return new Response(ResponseValues.Error, { error: e });
        }
    }

    async function deleteDEK() {
        try {
            await KeyManager.deleteDEK();
            return new Response(ResponseValues.Success);
        } catch(e) {
            return new Response(ResponseValues.Error, { error: e });
        }
    }

    async function generateDEK(request) {
        try {
            const user = await SAAuth.currentUserInfo();
            const username = user.username;
            
            await SAAuth.authenticateUser({
                username: username,
                password: request.parameters.password
            });

            return new Response(ResponseValues.Success);
        } catch(e) {
            return new Response(ResponseValues.Error, { error: e });
        }
    }

    async function getUserKeys() {
        try {
            const keys = await KeyManager.getUserKeys();
            return new Response(ResponseValues.Success, { keys });
        } catch(e) {
            return new Response(ResponseValues.Error, { error: e });
        }
    }
    
    async function setMasterPassword(request) {
        try {
            await KeyManager.setMasterPassword(request.password);
            return new Response(ResponseValues.Success);
        } catch(e) {
            return new Response(ResponseValues.Error, { error: e });
        }
    }

    if (messages[request.action]) {
        messages[request.action](request)
        .then((result) => {
            sendResponse(result);
        })
        .catch((error) => {
            sendResponse(error);
        });
    }
}
