import { Comm } from './Comm';

export class AuthHelper {
    static sendCredentialsToBackground = (credentials, fromOptions, fromOnboard) => {
        chrome.runtime.sendMessage({
            action: "AUTHENTICATE_USER",
            credentials: credentials,
            fromOptions: fromOptions,
            fromOnboard: fromOnboard
        }, (response) => {
    
        });
    }
    
    static sendSessionToBackground = (session, fromOptions, fromOnboard) => {
        chrome.runtime.sendMessage({
            action: "BUILD_USER_SESSION",
            session: session,
            fromOptions: fromOptions,
            fromOnboard: fromOnboard
        }, (response) => {
    
        });
    }
    
    static injectAuthScript = () => {
        const messageHandler = (event) => {
            switch (event.detail.messageType) {
                case 'CREDS':
                    this.sendCredentialsToBackground(event.detail.credentials, event.detail.fromOptions, event.detail.fromOnboard);
                    break;
                case 'SESSION':
                    this.sendSessionToBackground(event.detail.session, event.detail.fromOptions, event.detail.fromOnboard);
                    break;
                default:
                    break;
            }
        }

        let comm = new Comm();
        comm.injectScript();
        document.addEventListener(comm.getChannel(), function (event) {
           messageHandler(event);
        });
    }
}