import { DomTools } from './DomTools';
import { Detector } from '../../Tools/Detector';
import { Tools } from '../../Tools/Tools';

const Steps = {
    findPositionFixed: "FIND_POSITION_FIXED",
    hideCurrent: "HIDE_CURRENT",
    setContext: "SET_CONTEXT",
    click: "CLICK",
    hideBackground: "HIDE_BACKGROUND",
    hideParent: "HIDE_PARENT",
    closeWindow: "CLOSE_WINDOW",
    clickChildFilter: "CLICK_CHILD_FILTER",
    findButtonClick: "FIND_BUTTON_CLICK",
    domNavigate: "DOM_NAVIGATE",
    expandElements: "EXPAND_ELEMENTS",
    getCookieItems: "GET_COOKIE_ITEMS"
}

export class Step {
    constructor(step, userPreferences, currentDom) {
        this.step = step;
        this.userPreferences = userPreferences;
        this.currentDom = currentDom;
        this.clickCount = 0;
        this.cookieItems = null;
        this.finishExecution = false;
    }

    async executeStep() {
        const self = this;

        const steps = {
            [Steps.findPositionFixed]: findPositionFixed,
            [Steps.hideCurrent]: hideCurrent,
            [Steps.setContext]: setContext,
            [Steps.click]: click,
            [Steps.hideBackground]: hideBackground,
            [Steps.hideParent]: hideParent,
            [Steps.closeWindow]: closeWindow,
            [Steps.clickChildFilter]: clickChildFilter,
            [Steps.findButtonClick]: findButtonClick,
            [Steps.domNavigate]: domNavigate,
            [Steps.expandElements]: expandElements,
            [Steps.getCookieItems]: getCookieItems
        }

        async function findPositionFixed() {
            try {
                const element = 
                    await DomTools.findPositionFixed(self.currentDom, self.step.selector, self.step.timeout, self.step.filter.split(","));

                self.currentDom = element;
            } catch(e) {
                Tools.debugLog("Error in findPositionFixed.");
                Tools.debugLog(e, true);
                throw new Error(e);
            }
        }

        async function domNavigate() {
            try {
                const detector = new Detector(self.currentDom, self.step.selector.split(","));
                let newDom = await detector.detectSelector(self.step.timeout || 1000);

                if (self.step.shadowRoot) {
                    newDom = newDom.shadowRoot;
                }

                self.currentDom = newDom;

                return;
            } catch(e) {
                Tools.debugLog("Error in domNavigate.");
                Tools.debugLog(e, true);
                self.finishExecution = true;
                throw new Error(e);
            }
        }

        async function click() {
            try {
                const buttonClickDetector = new Detector(self.currentDom, self.step.selector.split(","));
                const buttonClick = await buttonClickDetector.detectSelector(self.step.timeout ? self.step.timeout : 1000);
                if (self.step.eventDispatch) {
                    buttonClick.dispatchEvent(new Event('click'));
                } else {
                    buttonClick.click();
                }
                self.clickCount++;
                return;
            } catch(e) {
                Tools.debugLog("Error in click.");
                Tools.debugLog(e, true);
                throw new Error(e);
            }
        }

        async function hideCurrent() {
            DomTools.hideNode(self.currentDom);
            return;
        }

        async function setContext() {
            self.currentDom = document.querySelector(self.step.selector);
            return;
        }

        async function hideBackground() {
            if (self.userPreferences.background) {
                if (self.step.background) {
                    for (const bg of self.step.background.split(",")) {
                        try {
                            const detector = new Detector(document, [bg]);
                            const element = await detector.detectSelector(self.step.timeout ? self.step.timeout : 1000);
                            DomTools.hideNode(element);
                        } catch(e) {
                            
                        }
                    }
                }
            }
            return;
        }

        async function hideParent() {
            try {
                const element = self.currentDom.querySelector(self.step.selector).parentElement;
                DomTools.hideNode(element);
            } catch(e) {
                Tools.debugLog("Error in hideParent.");
            }
            return;
        }

        async function closeWindow() {
            await click();
            self.currentDom = self.currentDom.parentElement;
            return;
        }

        async function clickChildFilter() {
            try {
                const button = 
                    await DomTools.findChildren(self.currentDom, self.step.selector, self.step.timeout, self.step.filter.split(","));
                button.click();
                self.clickCount++;
                return;
            } catch(e) {
                Tools.debugLog("Error in clickChildFilter.");
                Tools.debugLog(e, true);
                throw new Error(e);
            }
        }

        async function findButtonClick() {
            try {
                const elements = 
                    await DomTools.findElements(self.currentDom, self.step.selector, self.step.timeout, self.step.filter.split(","));
                for (const element of elements) {
                    element.click();
                    self.clickCount++;
                }
                return;
            } catch(e) {
                Tools.debugLog("Error in findButtonClick.");
                Tools.debugLog(e, true);
                throw new Error(e);
            }
        }

        async function expandElements() {
            try {
                const elements = self.currentDom.querySelectorAll(self.step.selector);
                elements.forEach(e => {
                    e.click();
                });
                return;
            } catch(e) {
                Tools.debugLog("Error in expandElements.");
                Tools.debugLog(e, true);
                throw new Error(e);
            }
        }

        async function getCookieItems() {
            try {
                const detector = new Detector(self.currentDom, self.step.selector.split(","));
                const cookieItems = await detector.detectSelectors(self.step.timeout ? self.step.timeout : 1000);
                self.cookieItems = cookieItems;
            } catch(e) {
                Tools.debugLog("Error in getCookieItems.");
                Tools.debugLog(e, true);
                throw new Error(e);
            }
            return;
        }

        Tools.debugLog("Running STEP " + self.step.type);
        if (steps[self.step.type]) {
            if (self.step.delay) {
                Tools.debugLog("Awaiting for " + self.step.delay + "ms");
                await Tools.delay(self.step.delay);
                Tools.debugLog("Finished waiting.");
            }

            try {
                await steps[self.step.type]();
                return self;
            } catch(e) {
                throw new Error(e);
            }
        }
    }
}