import { Response } from './Response';
import { Tools } from '../Tools/Tools';

export class Request {
    constructor(action, parameters) {
        this.message = {
            action,
            parameters
        }
    }

    sendRequest() {
        Tools.debugLog("Sending message: " + JSON.stringify(this.message));
        return new Promise((resolve, reject) => {
            chrome.runtime.sendMessage(this.message, (response) => {
                if (response) {
                    const obj = new Response(
                        response.message.status, 
                        response.message.payload, 
                        response.message.code
                    );

                    if (obj.isOk()) {
                        resolve(obj);
                    }
                    if (obj.isError()) {    
                        Tools.debugLog("Error in Request from message: ");
                        Tools.debugLog(this.message, true);
                        Tools.debugLog(obj, true);
                        reject(obj);
                    }
                } else {
                    reject(new Error("No response received."));
                }
            });
        });
    }

    sendRequestToTab(tabId) {
        Tools.debugLog("Sending message: " + JSON.stringify(this.message));
        return new Promise((resolve, reject) => {
            chrome.tabs.sendMessage(tabId, this.message, (response) => {
                if (response) {
                    const obj = new Response(
                        response.message.status,
                        response.message.payload,
                        response.message.code
                    );

                    if (obj.isOk()) {
                        resolve(obj);
                    }
                    if (obj.isError()) {
                        Tools.debugLog("Error in Request from message: ");
                        Tools.debugLog(this.message, true);
                        Tools.debugLog(obj, true);
                        reject(obj);
                    }
                } else {
                    reject(new Error("No response received."));
                }
            });
        });
    }
}
