import { Messages, Response, ResponseValues } from '../../shared';
import { Entitlement, SubscriptionVerificationNotification } from './Entitlement';

chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
    processMessage(request, sendResponse);
    return true;
});

function processMessage(request, sendResponse) {
    const messages = {
        [Messages.Entitlement.GetUserEntitlement]: getUserEntitlement,
        [Messages.Entitlement.IncrementCounter]: incrementCounter,
        [Messages.Entitlement.GetCurrentEntitlement]: getCurrentEntitlement,
        [Messages.Entitlement.GetCounter]: getCounter,
        [Messages.Entitlement.SubscriptionVerificationNotification.GetPopupShownAt]: getVerifyPopupShownAt,
        [Messages.Entitlement.SubscriptionVerificationNotification.ShowPopup]: showVerifyPopup,
    }

    async function getUserEntitlement() {
        try {
            const hasEntitlement = await Entitlement.getUserEntitlement();
            const isRCIdSynced = await Entitlement.getIsRCIdSynced()
            const installedAt = await Entitlement.getRCIdInstalledAt()

            return new Response(ResponseValues.Success, {
                hasEntitlement: hasEntitlement,
                isRCIdSynced,
                installedAt,
            });
        } catch(e) {
            return new Response(ResponseValues.Success, {
                hasEntitlement: true,
                isRCIdSynced: true,
                installedAt: null
            });
        }
    }

    async function incrementCounter(request) {
        try {
            await Entitlement.incrementCounter({ hostname: request.parameters.hostname });
            return new Response(ResponseValues.Success, { });
        } catch(e) {
            return new Response(ResponseValues.Error, { });
        }
    }

    async function getCurrentEntitlement(){
        try {
            const hasEntitlement = await Entitlement.getCurrentEntitlement();
            const isRCIdSynced = await Entitlement.getIsRCIdSynced()
            const installedAt = await Entitlement.getRCIdInstalledAt()

            return new Response(ResponseValues.Success, {
                hasEntitlement: hasEntitlement,
                isRCIdSynced,
                installedAt
            });
        } catch(e) {
            return new Response(ResponseValues.Error, {
                hasEntitlement: true,
                isRCIdSynced: true,
                installedAt: null
            });
        }
    }

    async function getCounter() {
        try {
            const counter = await Entitlement.getCounter();
            return new Response(ResponseValues.Success, { counter: counter });
        } catch(e) {
            return new Response(ResponseValues.Error, { counter: 0 });
        }
    }

    async function getVerifyPopupShownAt() {
        try {
            const popupShownAt = await SubscriptionVerificationNotification.getPopupShownAt()
            return new Response(ResponseValues.Success, { popupShownAt: popupShownAt });
        } catch (e) {
            return new Response(ResponseValues.Error, { popupShownAt: null });
        }
    }

    async function showVerifyPopup() {
        try {
            const url = chrome.runtime.getURL("update.html");
            await chrome.tabs.create({ url });
            await SubscriptionVerificationNotification.setPopupShownAt()
            return new Response(ResponseValues.Success, {});
        } catch (e) {
            return new Response(ResponseValues.Error, {});
        }
    }

    if (messages[request.action]) {
        messages[request.action](request)
        .then((result) => {
            sendResponse(result);
        })
        .catch((error) => {
            sendResponse(error);
        });
    }
}
