import { UserPreferences } from "./UserPreferences";
import { SAAuth } from "../Auth/SAAuth";

import { Messages, Response, ResponseValues } from '../../shared';

chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
    processMessage(request, sendResponse);
    return true;
});

function processMessage(request, sendResponse) {
    const messages = {
       [Messages.UserPreferences.GetUserPreferences]: getUserPreferences,
       [Messages.UserPreferences.SetUserPreferences]: setUserPreferences
    }

    async function getUserPreferences() {
        try {
            const isLoggedIn = await SAAuth.isLoggedIn();
            const userPreferences = await UserPreferences.getUserPreferences(isLoggedIn);
            return new Response(ResponseValues.Success, { userPreferences });
        } catch(e) {
            return new Response(ResponseValues.Success, { userPreferences: UserPreferences.getDefaultPreferences() });
        }
    }

    async function setUserPreferences(request) {
        try {
            const isLoggedIn = await SAAuth.isLoggedIn();
            await UserPreferences.setUserPreferences(request.parameters.userPreferences, isLoggedIn)
            return new Response(ResponseValues.Success);
        } catch(e) {
            return new Response(ResponseValues.Error);
        }
    }

    if (messages[request.action]) {
        messages[request.action](request)
        .then((result) => {
            sendResponse(result);
        })
        .catch((error) => {
            sendResponse(error);
        });
    }
}