import awsconfigdev from '../Config/aws-exports-devtest';
import awsconfig from '../Config/aws-exports';

export default class OpenAccountPage {
    static isDev = () => {
        return awsconfig.aws_user_pools_id === awsconfigdev.aws_user_pools_id;
    }

    static openSignIn = async () => {
        const domain = this.isDev() ? "dev" : "account";
        return new Promise((resolve, reject) => {
            chrome.tabs.create({url: `https://${domain}.super-agent.com/signin?fromExtension=true`}, (tab) => {
                resolve(tab);
            });
        });
    }

    static openSignUp = async (fromOnboard) => {
        const domain = this.isDev() ? "dev" : "account";
        return new Promise((resolve, reject) => {
            const param = fromOnboard ? "?fromOnboard=true" : "?fromExtension=true";
            chrome.tabs.create({url: `https://${domain}.super-agent.com/signup${param}`}, (tab) => {
                resolve(tab);
            });
        });
    }

    static openForgotPassword = async (fromOnboard) => {
        const domain = this.isDev() ? "dev" : "account";
        return new Promise((resolve, reject) => {
            const param = fromOnboard ? "?fromOnboard=true" : "?fromExtension=true";
            chrome.tabs.create({url: `https://${domain}.super-agent.com/resetpassword${param}`}, (tab) => {
                resolve(tab);
            });
        });
    }
}