export const themeColors = {
    "firefoxDark": "#2A2A2E",
    "firefoxDarkRGB": "rgb(43, 42, 51)"
}

export class Theme {
    constructor() {
        this.osDark = window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches;
    }

    async getFirefoxTheme() {
        try {
            const theme = await browser.theme.getCurrent();
            
            this.firefoxDark = 
                theme.colors["ntp_background"] === themeColors.firefoxDark ||
                theme.colors["ntp_background"] === themeColors.firefoxDarkRGB;

        } catch (e) {
            this.firefoxDark = false;
        }

        return this.firefoxDark;
    }

    async isDarkTheme() {
        return this.osDark || (await this.getFirefoxTheme());
    }
}