import { API } from "aws-amplify";
import b64 from 'base64-arraybuffer';
import { v4 as uuidv4 } from 'uuid';

import BrowserStorage from '../BackgroundTools/BrowserStorage';
import { StorageKeys } from '../BackgroundTools/StorageKeys';
import { KeyManager } from '../KeyManager/KeyManager';
import { SAAuth } from '../Auth/SAAuth';
import { Tools } from '../../Tools/Tools';
import { Crypto } from '../../Tools/Crypto';
import DateFormat from '../../views/Shared/DateFormat';

import { ExpirationDateOption, ExpirationDates } from "../../views/Shared/ExpirationDates";

// import { TextEncoder } from 'util';

import './LoggerMessage';

export class Logger {

    static async getPreferences(isLoggedIn) {
        return new Promise(async (resolve, _) => {
            BrowserStorage.getKey(StorageKeys.Logger.Preferences, isLoggedIn)
                .then((results) => {
                    resolve(results.preferences);
                })
                .catch(async () => {

                    if (isLoggedIn) {
                        const apiName = 'logger';
                        const path = '/preferences';

                        const myInit = {
                            headers: {},
                            response: true,
                        };

                        const res = await API.get(apiName, path, myInit);
                        const data = res.data;

                        if (!this.preferencesAreValid(data)) {

                            await this.setDefaultPreferences();
                            let defaultPreferences = await this.getDefaultPreferences();

                            resolve(defaultPreferences.preferences);
                            return

                        }

                        const preferencesObject = {
                            preferences: data,
                            expirationDate: ExpirationDates.expirationDateInStringFor(ExpirationDateOption.PREFERENCES)
                        }

                        await BrowserStorage.setKey(
                            StorageKeys.Logger.Preferences, 
                            preferencesObject
                        );

                        resolve(preferencesObject.preferences);
                    } else {
                        await this.setDefaultPreferences();

                        let defaultPreferences = await this.getDefaultPreferences();
                        await BrowserStorage.setKey(
                            StorageKeys.Logger.Preferences, 
                            defaultPreferences
                        );

                        resolve(defaultPreferences.preferences);
                    }
                })
        });
    }

    static preferencesAreValid(data) {
        return data.enabled !== undefined && data.retention !== undefined;
    }

    static createPreferences(logging, retention) {
        return {
            preferences: {
                enabled: logging,
                retention: retention
            },
            expirationDate: ExpirationDates.expirationDateInStringFor(ExpirationDateOption.PREFERENCES)
        }
    }

    static async getDefaultPreferences(isLoggedIn) {
        let logs = await this.getLogs(isLoggedIn);
        let hasLogs = logs.length > 0;

        const loggingEnabled = hasLogs;
        const retention = 30;

        return this.createPreferences(loggingEnabled, retention);
    }

    static async setDefaultPreferences() {
        const data = await this.getDefaultPreferences();

        await BrowserStorage.setKey(StorageKeys.Logger.Preferences, data);
    }

    static setPreferences(isLoggedIn, logging) {
        return this.getPreferences(isLoggedIn).then((preferences) => {

            return new Promise(async (resolve, reject) => {
                const apiName = 'logger';
                const path = '/preferences';

                const body = {
                    "enabled": logging,
                    "retention": preferences.retention ? preferences.retention : 30
                };

                // Save Locally
                const data = this.createPreferences(logging, preferences.retention ? preferences.retention : 30)

                await BrowserStorage.setKey(StorageKeys.Logger.Preferences, data);

                // Send To Server
                const myInit = {
                    headers: {},
                    body: body
                };

                try {
                    await API.post(apiName, path, myInit);
                    resolve();
                } catch (e) {
                    reject(e);
                }
            });

        }).catch((e) => {

        });
    }

    static calculateTimeSaved(executionResults) {
        return (executionResults.totalClicks * 3) / 60;
    }

    static async updateMetrics(executionResults) {
        const input = {
            cc: executionResults.totalClicks,
            ws: 1,
            ms: this.calculateTimeSaved(executionResults)
        }

        const isLoggedIn = await SAAuth.isLoggedIn();
        await this.updateUsageMetrics(input, isLoggedIn);
    }

    static async prepareWriteLog(executionResults) {    
        Tools.debugLog("[CONSENT TRAIL DEBUG] Inside prepareWriteLog.");    
        const time = DateFormat.currentDateString();
        executionResults.time = time;

        const isLoggedIn = await SAAuth.isLoggedIn();
        const canWriteLog = await this.canWriteLog(executionResults.hostname, isLoggedIn);

        Tools.debugLog("[CONSENT TRAIL DEBUG] canWriteLog ? " + canWriteLog);

        if (canWriteLog) {
            Tools.debugLog("Inside prepareWriteLog. Testing canWriteLog.");
            try {
                Tools.debugLog("Yes");
                await this.writeLog(executionResults, isLoggedIn);
            } catch(e) {
                Tools.debugLog("Error writing log: " + e);
            }
        }
    }

    static canWriteLog = async (hostname, isLoggedIn) => {
        const hostnameIsEmpty = !(hostname.length > 0);
        const hereToday = await this.haveLogToday(hostname, isLoggedIn);
        const loggingPreferences = await this.getPreferences(isLoggedIn);

        return !hostnameIsEmpty && !hereToday && loggingPreferences.enabled;
    }

    static haveLogToday(hostname, isLoggedIn) {
        return new Promise( (resolve, _) => {
            this.getLogs(isLoggedIn)
            .then((logs) => {
                let filteredLogs =
                    logs.filter( (log) => {
                        const logDate = new Date(log.date);
                        return DateFormat.isToday(logDate);
                    });
                let result = filteredLogs.some(log => log['hostname'] === hostname);

                resolve(result);
            })
            .catch( () => {
                resolve(false);
            });
        });
    }

    static async getLogs(isLoggedIn) {
        return new Promise(async (resolve, reject) => {
            BrowserStorage.getKey(StorageKeys.Logger.Logs, isLoggedIn)
                .then((results) => {
                    resolve(results.logs);
                })
                .catch(async () => {
                    if (isLoggedIn) {
                        try {
                            const DEK = await KeyManager.getDEK();
                            const apiName = 'logger';
                            const path = '/logs';

                            const myInit = {
                                headers: {},
                                response: true,
                            };
                            const res = await API.get(apiName, path, myInit);

                            let decryptedLogs = [];
                            const dc = new TextDecoder();

                            for (const encryptedLog of res.data) {
                                try {
                                    const decrypted = await Crypto.decrypt(b64.decode(encryptedLog.log), DEK, b64.decode(encryptedLog.iv));
                                    const logBuffer = b64.decode(decrypted);
                                    decryptedLogs.push({
                                        ...JSON.parse(dc.decode(logBuffer)),
                                        pk: encryptedLog.pk,
                                        sk: encryptedLog.sk
                                    });
                                } catch (e) {

                                }
                            }

                            decryptedLogs.sort((a, b) => { return a.dateTime > b.dateTime ? -1 : 1 });

                            const data = {
                                logs: decryptedLogs,
                                expirationDate: ExpirationDates.expirationDateInStringFor(ExpirationDateOption.LOGS)
                            }

                            await BrowserStorage.setKey(StorageKeys.Logger.Logs, data);
                            resolve(data.logs);
                        } catch (e) {
                            Tools.debugLog("Error getting logs: " + e);
                            reject(e);
                        }
                    } else {
                        // User is not logged in and there are no logs in cache. Init empty cache. No need to use expiration date.
                        const data = {
                            logs: []
                        };

                        await BrowserStorage.setKey(StorageKeys.Logger.Logs, data);
                        resolve(data.logs);
                    }
                })
        });
    }

    static async deleteAllLogsFromCache() {
        await BrowserStorage.clearKey(StorageKeys.Logger.Logs);
    }

    static async deleteLogsFromCache(logList) {
        // 1 - Get logs from cache
        // 2 - Keep only ones not in list
        // 3 - Update cache
        BrowserStorage.getKey(StorageKeys.Logger.Logs)
            .then(async (results) => {
                let cachedLogs = results.logs;
                let updatedLogs = [];

                // eslint-disable-next-line
                cachedLogs.map((log) => {
                    if (!logList.includes(log.sk)) {
                        updatedLogs.push(log);
                    }
                });

                const data = {
                    logs: updatedLogs
                }

                await BrowserStorage.setKey(StorageKeys.Logger.Logs, data);
            })
            .catch((e) => {

            })
    }

    static async writeLogToCache(log, isLoggedIn) {
        BrowserStorage.getKey(StorageKeys.Logger.Logs, isLoggedIn)
            .then(async (results) => {
                let cachedLogs = results.logs;

                // Log limit reached, popping last element
                if (cachedLogs.length === 50) {
                    cachedLogs.pop();
                }

                cachedLogs.unshift(log);

                const data = {
                    logs: cachedLogs,
                    expirationDate: isLoggedIn ? ExpirationDates.expirationDateInStringFor(ExpirationDateOption.LOGS) : null
                }

                await BrowserStorage.setKey(StorageKeys.Logger.Logs, data);
            })
            .catch(async () => {
                let logs = [];
                logs.unshift(log);

                const data = {
                    logs: logs,
                    expirationDate: ExpirationDates.expirationDateInStringFor(ExpirationDateOption.LOGS)
                }

                await BrowserStorage.setKey(StorageKeys.Logger.Logs, data);
            })
    }

    static async deleteAllLogs(isLoggedIn) {
        try {
            await this.deleteAllLogsFromCache()
        } catch (e) {
        }

        if (isLoggedIn) {

            return new Promise(async (resolve, reject) => {
                const apiName = 'logger';
                const path = '/logs';

                const myInit = {
                    headers: {},
                    body: {}
                };

                try {
                    await API.del(apiName, path, myInit);
                    resolve();
                } catch (e) {
                    reject();
                }
            });
        }
    }

    static async deleteLogs(logList, isLoggedIn) {
        // - Add logs to cache in Write Log as well
        if (isLoggedIn) {
            return new Promise(async (resolve, reject) => {
                // Need to delete logs remotely and update log cache as well.
                const apiName = 'logger';
                const path = '/logs';

                const myInit = {
                    headers: {},
                    body: logList,
                };

                try {
                    await API.del(apiName, path, myInit);
                    resolve();
                } catch (e) {
                    reject();
                }
            });
        } else {
            try {
                await this.deleteLogsFromCache(logList);
            } catch (e) {

            }
        }
    }

    static parseLog(log) {
        log.dateTime = log.time;

        log.timeSaved = log.totalClicks * 2 + log.cookieItems * 4;

        log.time = log.time.replace('T', ' ').replace('Z', '');

        log.date = log.time.split(' ')[0];
        log.time = log.time.split(' ')[1];
        log.time = log.time.split('.')[0];

        switch (log.outcome) {
            case 'PREFERENCES_APPLIED':
                log.action = "Your preferences."
                break;
            case 'ACCEPTED_DEFAULT':
                log.action = "Default."
                break;
            case 'ACCEPTED_NECESSARY':
                log.action = "Accepted necessary."
                break;
            case 'REJECTED_ALL':
                log.action = "Rejected all."
                break;
            case 'NONE':
                break;
            default:
                break;
        }

        return log;
    }

    static async writeLog(inputLog, isLoggedIn) {
        Tools.debugLog("writeLog()");
        const log = this.parseLog(inputLog);

        // Random sk so we can then identify log
        log.sk = uuidv4();

        if (!isLoggedIn) {
            await this.writeLogToCache(log);
        } else {
            // TODO: Why do we have a function to check if there is a DEK and then retrieve it and not just try to retrieve it directly?
            KeyManager.checkDEK()
                .then(async () => {
                    try {
                        Tools.debugLog("writeLog() after checkDEK");
                        const ec = new TextEncoder();
                        let DEK = await BrowserStorage.getKey(StorageKeys.KeyManager.DEK);

                        const encryptedLog = await Crypto.encrypt(ec.encode(JSON.stringify(log)), DEK);
                        await this.commitLog(encryptedLog);
                    } catch (e) {
                        Tools.debugLog("An Error occurred when trying commit log to server: " + e);
                    }
                }).catch(e => {
                    Tools.debugLog("[CONSENT TRAIL DEBUG] Checking DEK failed.");
                    BrowserStorage.getKey(StorageKeys.Logger.Backlog, isLoggedIn)
                        .then(async (results) => {
                            results.push(log);
                            await BrowserStorage.setKey(StorageKeys.Logger.Backlog, results);
                        })
                        .catch(async (e) => {
                            Tools.debugLog("[CONSENT TRAIL DEBUG] Everything failed.");
                            let logs = [];
                            logs.push(log);
                            await BrowserStorage.setKey(StorageKeys.Logger.Backlog, logs);
                        })
                });
        }
    }

    static commitLog(encryptedLog) {
        return new Promise(async (resolve, reject) => {
            const apiName = 'logger';
            const path = '/logs';
            const myInit = {
                headers: {},
                body: encryptedLog
            };

            try {
                await API.post(apiName, path, myInit);
                resolve();
            } catch (e) {
                reject(e);
            }
        });
    }

    static async writeBacklog() {
        BrowserStorage.getKey(StorageKeys.Logger.Backlog)
            .then(async (results) => {
                results.forEach(log => {
                    this.writeLog(log);
                })
                await BrowserStorage.clearKey(StorageKeys.Logger.Backlog);
            }).catch(() => {

            });
    }

    static async getUsageMetrics(isLoggedIn) {

        return new Promise(async (resolve, reject) => {
            BrowserStorage.getKey(StorageKeys.Logger.UsageMetrics, isLoggedIn)
                .then((results) => {
                    resolve(results.metrics);
                })
                .catch(async () => {
                    const apiName = 'logger';
                    const path = '/metrics';

                    const myInit = {
                        headers: {},
                        response: true,
                    };
                    try {
                        let res;
                        let data;

                        if (isLoggedIn) {
                            res = await API.get(apiName, path, myInit);

                            data = {
                                metrics: res.data,
                                expirationDate: ExpirationDates.expirationDateInStringFor(ExpirationDateOption.USAGE_METRICS)
                            }

                            await BrowserStorage.setKey(StorageKeys.Logger.UsageMetrics, data);
                            resolve(res.data);
                        } else {
                            // No user logged in and nothing in cache
                            data = {
                                metrics: {
                                    ws: 0,
                                    cc: 0,
                                    ms: 0
                                }
                            }
                        }

                        await BrowserStorage.setKey(StorageKeys.Logger.UsageMetrics, data);
                        resolve(data.metrics);
                    } catch (e) {
                        reject(e);
                    }
                })
        });
    }

    static async updateUsageMetrics(metrics, isLoggedIn) {
        const apiName = 'logger';
        const path = '/metrics';

        const myInit = {
            headers: {},
            body: metrics
        };

        if (!isLoggedIn) {
            try {
                const existingMetrics = await BrowserStorage.getKey(StorageKeys.Logger.UsageMetrics, isLoggedIn);

                const updatedMetrics = {
                    ws: existingMetrics.metrics.ws + metrics.ws,
                    cc: existingMetrics.metrics.cc + metrics.cc,
                    ms: existingMetrics.metrics.ms + metrics.ms
                };

                const data = {
                    metrics: updatedMetrics
                };

                await BrowserStorage.setKey(StorageKeys.Logger.UsageMetrics, data);
            } catch(e) {
                // No metrics in BrowserStorage
                const newMetrics = {
                    ws: metrics.ws,
                    cc: metrics.cc,
                    ms: metrics.ms
                };

                const data = {
                    metrics: newMetrics
                };

                await BrowserStorage.setKey(StorageKeys.Logger.UsageMetrics, data);
            }
        }

        try {
            await API.post(apiName, path, myInit);
            return;
        } catch (e) {
            throw new Error(e);
        }
    }

    static clearMetricsAndLogs() {
        return BrowserStorage.clearMetricsAndLogs();
    }
    
}