// Owned
import { Messages, Request } from '../shared';
import { URLHelper } from './URLHelper';
import { AuthHelper } from './AuthHelper';
import { Run } from './Run';
import { Notifications } from './Notifications';

// Tools imports
import { Tools } from '../Tools/Tools';

import awsconfig from '../Config/aws-exports';
import { OUTCOMES, RunStatus } from './RunStatus';
import { isEligible } from '../views/Shared/Functions/isEligible';
import { BackgroundTools } from '../Background/BackgroundTools/BackgroundTools';
import { initDomListener } from './Dom/DomMessage'

const env = awsconfig.aws_user_pools_id === 'eu-west-1_iv6CkI7wU' ? 'dev' : 'prod';

const getTabId = async (url) => {
    const results = await new Request(Messages.Tab.GetCurrentTab, { url }).sendRequest();
    const tab = results.message.payload.tab;

    if (tab) {
        return tab.id;
    } else {
        return null;
    }
}

const init = async () => {
    Tools.debugLog("init");
    URLHelper.setup();

    Tools.debugLog("URLHelper setup done");

    if (window === window.top) {
        initDomListener()
        Tools.debugLog("Going to notify");
        try {
            await Notifications.notifyContentScriptStarted();
        } catch(e) {
            Tools.debugLog("Error notifying");
        }
        Tools.debugLog("Notified content script started");

        let currentURL = window.location.href;
        let hostname = window.location.hostname;
    
        const tabId = await getTabId(currentURL);
        
        Tools.debugLog("Tabid: " + tabId);

        try {
            const results = 
                await new Request(Messages.Tab.GetExecutionResults, { tabId }).sendRequest();

            const currentExecutionResults = results.message.payload.executionResults;

            if (Tools.checkAutoRefresh(currentExecutionResults, 5, hostname)) {
                await Notifications.notifyTabFinished(tabId, hostname);
                return false;
            }
        } catch(e) {
            Tools.debugLog("Error getting results");
        }

        Tools.debugLog("Continuing");

        await new Request(Messages.Tab.ClearExecutionResults, { tabId }).sendRequest();
        await new Request(Messages.Generic.ExtensionIsWorking).sendRequest();

        if (hostname === 'account.super-agent.com'
            || hostname === 'dev.super-agent.com'
            || (window.location.hostname === 'localhost' && env === 'dev')) {
            AuthHelper.injectAuthScript();
        }

        Tools.debugLog("Returning true in init");
        return true;
    }

    Tools.debugLog("Returning true in init");
    return true;
}


    // getEligibility needs to:
    // 1 - Find if the user has an entitlement
    // 2 - If not, find how many times SA has worked today
    // 3 - Return true/false to determine whether we proceed
    // We should only display something to the user if we found a provider and are not running rules due to eligibility
const getEligibility = async () => {    
    const entitlementResult =
        await new Request(Messages.Entitlement.GetUserEntitlement, { }).sendRequest();
    
    const isEntitled = entitlementResult.message.payload.hasEntitlement;
    const isRCIdSynced = entitlementResult.message.payload.isRCIdSynced;
    const installedAt = entitlementResult.message.payload.installedAt;

    const isVerifiedSubscriber = isEligible(isEntitled, isRCIdSynced, installedAt)
    if (isEntitled && !isVerifiedSubscriber) {
        BackgroundTools.showSubscriptionVerificationNotification()
    }

    return isVerifiedSubscriber;
}

const startProcess = async () => {
    Tools.debugLog("Inside startProcess");

    let currentURL = await URLHelper.getCurrentUrl();

    if (currentURL == null) {
        Tools.debugLog("Returning due to currentURL null");
        return;
    }

    let hostname = Tools.hostnameForURL(currentURL);
    let rootDomain = Tools.rootDomainForURL(currentURL);
    let isBlockedResults = 
        await new Request(Messages.Whitelist.HostnameIsBlocked, { hostname: rootDomain }).sendRequest();
    let isBlocked = isBlockedResults.message.payload.isBlocked;

    Tools.debugLog("Before is blocked.");
    Tools.debugLog("Hostname: " + hostname);

    if (isBlocked) {
        Tools.debugLog("Returning as website is blocked.");
        await new Request(Messages.Tab.NotifyActiveTabIsBlocked).sendRequest();
        return;
    }

    let detectTimeout = 5000;
    const tabId = await getTabId(currentURL);

    Tools.debugLog("Tabid: " + tabId);
    let runStatus = new RunStatus(OUTCOMES.nothingFound);

    try {
        const isEligible = await getEligibility();
        if (window === window.top) {
            // Only run for providers if there are no URL rules
            runStatus = await Run.runForURL(rootDomain, hostname, tabId, isEligible);

            if (runStatus.outcome === OUTCOMES.nothingFound) {
                runStatus = await Run.runForProviders(hostname, tabId, detectTimeout, isEligible);
                if (runStatus.outcome === OUTCOMES.foundSomething) {
                    Tools.debugLog("Executed provider rules.");
                }
            } else {
                Tools.debugLog("Executed something in URL rules.");
            }
        } else {
            runStatus = await Run.runForProviders(hostname, tabId, detectTimeout, isEligible);
        }

        // on't show a tab per iFrameD
        if (runStatus.outcome === OUTCOMES.noEntitlement && window === window.top) {
            await Notifications.notifyNoEntitlement(tabId, hostname);
            return;
        }

        if (runStatus.outcome === OUTCOMES.foundSomething) {     
            Tools.debugLog("[CONTENT_SCRIPT_FINISHED]. Notifying at " + Date());   
            await Notifications.notifyTabFinished(tabId, hostname);
        }
    } catch (e) {        
        Tools.debugLog("Error in content script: ", e);
    }
};

Tools.debugLog("Content script started.");

async function startContentScript() {
    const shouldRun = await init();
    if (shouldRun) {
        await startProcess();
    }
}

startContentScript();
