// Owned
import NativeMessaging from "../../shared/NativeMessaging";
import i18n from "../../i18n.js";
import { UserPreferences } from '../UserPreferences/UserPreferences'
import { Whitelist } from '../Whitelist/Whitelist';
import { Entitlement } from "../Entitlement/Entitlement";

export class SARefreshable {

    static async refreshUserPreferences() {
        await UserPreferences.refreshUserPreferences();
    }

    static async refreshCurrentLanguage() {
        const currentLanguage = await NativeMessaging.sendNativeMessageIfPossibleWithResponse("CurrentLanguage");        
        i18n.changeLanguage(currentLanguage.language);
    }

    static async refreshWhitelist() {
        const response = await NativeMessaging.sendNativeMessageIfPossibleWithResponse("Whitelist");
        await Whitelist.saveDisabledHostnames(response.whitelist);
    }

    static async getUserEntitlement() {
        const response = await NativeMessaging.sendNativeMessageIfPossibleWithResponse("HasEntitlement");
        await Entitlement.setUserEntitlement(response.hasEntitlement);
        await Entitlement.setUserIsRCIdSynced(response.isRCIdSynced);
        await Entitlement.setRCIdExpiration(response);
    }

}
