import BrowserStorage from '../Background/BackgroundTools/BrowserStorage';
import { StorageKeys } from '../Background/BackgroundTools/StorageKeys';

let dataMemory = {};

// As per https://docs.amplify.aws/lib/auth/manageusers/q/platform/js#managing-security-tokens
export class TokenStorage {
    static syncPromise = null;

    static async setItem(key, value) {
        BrowserStorage.setKey(key, value)
        .then(() => {
            dataMemory[key] = value;
        });
    }

    // get item with the key
    static getItem(key) {
        // return BrowserStorage.getKey(key);
        return Object.prototype.hasOwnProperty.call(dataMemory, key) ? dataMemory[key] : undefined;
    }

    // remove item with the key
    static removeItem(key) {
        BrowserStorage.clearKey(key).then(() => {
            return delete dataMemory[key];
        });
    }

    // clear out the storage
    static clear() {
        dataMemory = {};
        return dataMemory;
    }

    static sync() {
        if (!TokenStorage.syncPromise) {
            TokenStorage.syncPromise =  new Promise((resolve, reject) => {
                BrowserStorage.getAll()
                .then((items) => {
                    const filteredKeys = Object.keys(items).filter( (key) => key.startsWith(StorageKeys.Cognito) );
                    filteredKeys.map( (key) => dataMemory[key] = items[key] );
                    resolve();
                })
            });
        }
        return TokenStorage.syncPromise;
    }
  }