import { Whitelist } from './Whitelist';
import { Messages, Response, ResponseValues } from '../../shared';

chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
    processMessage(request, sendResponse);
    return true;
});

function processMessage(request, sendResponse) {
    const messages = {
        [Messages.Whitelist.GetDisabledHostnames]: getDisabledHostnames,
        [Messages.Whitelist.SaveDisabledHostnames]: saveDisabledHostnames,
        [Messages.Whitelist.HostnameIsBlocked]: hostnameIsBlocked,
        [Messages.Whitelist.UpdateHostnameState]: updateHostnameState
    };

    async function getDisabledHostnames() {
        try {
            const disabledHostnames = await Whitelist.getDisabledHostnames();
            return new Response(ResponseValues.Success, { disabledHostnames });
        } catch(e) {
            return new Response(ResponseValues.Error, { error: e });
        }
    }

    async function saveDisabledHostnames(request) {
        try {
            await Whitelist.saveDisabledHostnames(request.parameters.disabledHostnames);
            return new Response(ResponseValues.Success);
        } catch(e) {
            return new Response(ResponseValues.Error, { error: e });
        }
    }

    async function hostnameIsBlocked(request) {
        try {
            const isBlocked = await Whitelist.hostnameIsBlocked(request.parameters.hostname);
            return new Response(ResponseValues.Success, { isBlocked });
        } catch(e) {
            return new Response(ResponseValues.Error, { error: e });
        }
    }

    async function updateHostnameState(request) {
        try {
            await Whitelist.updateHostnameState(request.parameters.hostname, request.parameters.state);
            return new Response(ResponseValues.Success);
        } catch(e) {
            return new Response(ResponseValues.Error, e);
        }
    }

    if (messages[request.action]) {
        messages[request.action](request)
        .then((result) => {
            sendResponse(result);
        })
        .catch((error) => {
            sendResponse(error);
        });
    }
}
