import { Action } from './Action';
import { Tools } from '../../Tools/Tools';

export class RulesEngine {

    constructor(userPreferences, actions) {
        this.userPreferences = userPreferences;
        this.actions = actions;
        this.currentDom = document;
        this.cookieItems = null;
        this.outcome = "";
        this.clickCount = 0;
    }

    canRejectAll() {
        return !this.userPreferences.some(p => p === true);
    }

    update(actionResults) {
        this.outcome = actionResults.outcome || "";
        this.clickCount += actionResults.clickCount;
        this.cookieItems = actionResults.cookieItems;
        this.currentDom = actionResults.currentDom || this.currentDom;
    }

    async execute() {
        for (const a of this.actions) {
            try {
                const action = new Action(a, this.userPreferences, this.currentDom, this.cookieItems);
                const actionResults = await action.executeAction();
                this.update(actionResults);
                
                if (actionResults.finishExecution === true) {
                    Tools.debugLog("Ordered to finish execution.");
                    break;
                }
                
            } catch(e) {
                Tools.debugLog("Error running action " + a.name);
                Tools.debugLog(e, true);
            }
        }

        Tools.debugLog("Finished actions");
        Tools.debugLog(this, true);
        return {
            totalClicks: this.clickCount,
            outcome: this.outcome
        };
    }
}