import { ExpirationDateOption, ExpirationDates } from '../ExpirationDates';

export const isExpired = installedAt => {
    return new Date() >= ExpirationDates.expirationDateFor(ExpirationDateOption.RC_ID_INSTALLED_AT, new Date(installedAt));
}

export const isEligible = (isEntitled, isRCIdSynced, installedAt) => {
    return isEntitled && (isRCIdSynced || !installedAt || !isExpired(installedAt));
}
