import { SAAuth } from './SAAuth';
import { KeyManager } from '../KeyManager/KeyManager';
import OpenAccountPage from '../../shared/OpenAccountPage';

import { Messages, Response, ResponseValues } from '../../shared';

let signInWindow;

chrome.runtime.onMessage.addListener((request, _, sendResponse) => {
    processMessage(request, sendResponse);
    return true;
});

function processMessage(request, sendResponse) {
    const messages = {
       [Messages.Auth.OpenSignIn]: openSignIn,
       [Messages.Auth.OpenSignUp]: openSignUp,
       [Messages.Auth.OpenForgotPassword]: openForgotPassword,
       [Messages.Auth.AuthenticateUser]: authenticateUser,
       [Messages.Auth.BuildUserSession]: buildUserSession,
       [Messages.Auth.SignInFromExtension]: signInFromExtension,
       [Messages.Auth.SignOut]: signOut,
       [Messages.Auth.CheckAuth]: checkAuth,
       [Messages.Auth.ChangeUserPassword]: changeUserPassword,
       [Messages.Auth.DeleteUser]: deleteUser
    };

    async function openSignIn() {
        const window = await OpenAccountPage.openSignIn()
        signInWindow = window;
        return new Response(ResponseValues.Success);
    }

    async function openSignUp(request) {
        const window = await OpenAccountPage.openSignUp(request.parameters.fromOnboard);
        signInWindow = window;
        return new Response(ResponseValues.Success);
    }

    async function openForgotPassword(request) {
        const window = await OpenAccountPage.openForgotPassword(request.parameters.fromOnboard);
        signInWindow = window;
        return new Response(ResponseValues.Success);
    }

    async function authenticateUser(request) {
        try {
            const user = await SAAuth.authenticateUser(request.credentials);
            chrome.tabs.remove(signInWindow.id);
            
            if (!request.fromOnboard) {
                chrome.runtime.openOptionsPage();
            }
            
            return new Response(ResponseValues.Success, user);
        } catch(e) {
            return new Response(ResponseValues.Error, e);
        }
    }

    async function signInFromExtension(request) {
        try {
            const user = await SAAuth.authenticateUser(request.parameters.credentials);
            return new Response(ResponseValues.Success, user);
        } catch(e) {
            return new Response(ResponseValues.Error, e);
        }
    }

    async function buildUserSession(request) {
        try {
            await SAAuth.buildUserSessionFromSession(request.session);
            chrome.tabs.remove(signInWindow.id);
            
            if (!request.fromOnboard) {
                chrome.runtime.openOptionsPage();
            }

            return new Response(ResponseValues.Success);
        } catch(e) {
            return new Response(ResponseValues.Error, e);
        }
    }

    async function signOut() {
        try {
            await SAAuth.logoutUser();
            return new Response(ResponseValues.Success);
        } catch(e) {
            return new Response(ResponseValues.Error, e);
        }
    }

    async function checkAuth() {
        try {
            const user = await SAAuth.currentAuthenticatedUser();
            return new Response(ResponseValues.Success, user);
        } catch(e) {
            return new Response(ResponseValues.Error, e);
        }
    }

    async function changeUserPassword(request) {
        try {
            await SAAuth.changePassword(request.parameters.oldPassword, request.parameters.newPassword);
            await KeyManager.changeMasterPassword(request.parameters.oldPassword, request.parameters.newPassword);

            return new Response(ResponseValues.Success);
        } catch(e) {
            return new Response(ResponseValues.Error, e);
        }
    }

    async function deleteUser() {
        try {
            await SAAuth.deleteUser();
            await SAAuth.logoutUser();

            return new Response(ResponseValues.Success);
        } catch(e) {
            return new Response(ResponseValues.Error, e);
        }
    }

    if (messages[request.action]) {
        messages[request.action](request)
        .then((result) => {
            sendResponse(result);
        })
        .catch((error) => {
            sendResponse(error);
        });
    }
} 

