export const ResponseValues = {
    Success: "OK",
    Error: "ERROR"
};  

export class Response {
    constructor(status, payload) {
        this.message = {
            status,
            payload
        }
    }

    isOk() {
        return this.message.status === ResponseValues.Success;
    }

    isError() {
        return this.message.status === ResponseValues.Error;
    }
}