import i18next from 'i18next';


/*
    From https://weeknumber.com/how-to/javascript
*/

export default class FormatDate {

    static dayOfYear = (date) => Math.floor((date - new Date(date.getFullYear(), 0, 0)) / (1000 * 60 * 60 * 24));

    static isYesterday(someDate) {
        const yesterday = new Date();
        yesterday.setDate(yesterday.getDate() - 1);
    
        return this.areEqual(someDate, yesterday);
    }

    static isToday(someDate) {
        const today = new Date();
    
        return this.areEqual(someDate, today);
    }

    static isCurrentWeek(someDate) {
        const today = new Date();
        return (this.dayOfYear(today) - this.dayOfYear(someDate)) <= 7;
    }

    static isCurrentMonth(someDate) {
        const today = new Date();
        return (this.dayOfYear(today) - this.dayOfYear(someDate)) <= 30;
    }

    static areEqual(date, otherDate) {
        return date.getDate() === otherDate.getDate() &&
            date.getMonth() === otherDate.getMonth() &&
            date.getFullYear() === otherDate.getFullYear();
    }

    static formatDate(date) {

        if (this.isToday(date) === true) {
            return i18next.t('general.date.today');
        } else if (this.isYesterday(date) === true) {
            return i18next.t('general.date.yesterday');
        } else {

            var options = { year: 'numeric', month: 'long', day: 'numeric' };
            return date.toLocaleDateString("en-US", options);

        }
    }

    static currentDateString() {
        let date = new Date();
        return new Date(date.getTime() - (date.getTimezoneOffset() * 60000)).toJSON();
    }

}