import { API } from "aws-amplify";
import b64 from 'base64-arraybuffer';

import { Logger } from '../Logger/Logger';
import BrowserStorage from '../BackgroundTools/BrowserStorage';
import { StorageKeys } from '../BackgroundTools/StorageKeys';
import { Crypto } from '../../Tools/Crypto';
import { Tools } from '../../Tools/Tools';

import './KMMessage';

export class KeyManager {
    static deleteDEK() {
        return new Promise((resolve, reject) => {
            BrowserStorage.clearKey(StorageKeys.KeyManager.DEK)
                .then(() => {
                    resolve();
                })
                .catch((e) => {
                    reject(e);
                });
        });
    }

    static checkDEK() {
        return new Promise((resolve, reject) => {
            BrowserStorage.getKey(StorageKeys.KeyManager.DEK)
                .then(() => {
                    resolve();
                })
                .catch((e) => {
                    reject(e);
                });
        });
    }

    static getDEK() {
        return new Promise((resolve, reject) => {
            BrowserStorage.getKey(StorageKeys.KeyManager.DEK)
                .then((dek) => {
                    resolve(dek);
                })
                .catch((e) => {
                    reject(e);
                });
        });
    }

    static async generateDEK(password) {
        return new Promise(async (resolve, reject) => {
            try {
                const ec = new TextEncoder();
                const userKeys = await this.getUserKeys();
                const KEK = await Crypto.generateKEK(ec.encode(password), b64.decode(userKeys.salt));
                const DEK = await Crypto.decrypt(b64.decode(userKeys.edek), KEK, b64.decode(userKeys.iv));

                await this.saveDEK(DEK);
                
                // Have to move this to Logger
                Logger.writeBacklog();
                resolve();
            } catch (e) {
                reject(e);
            }
        })
    }

    static async updateDEK(DEK) {
        try {
            if (DEK !== undefined) {
                await this.saveDEK(DEK);
            } else {
                await this.clearDEK();
            }
        } catch(e) {
            Tools.debugLog("Error updating DEK: " + e);
        }

    }

    static async saveDEK(DEK) {
        await BrowserStorage.setKey(StorageKeys.KeyManager.DEK, DEK);
    }

    static async clearDEK() {
        await BrowserStorage.clearKey(StorageKeys.KeyManager.DEK);
    }

    static getUserKeys() {
        return new Promise(async (resolve, _) => {
            BrowserStorage.getKey(StorageKeys.KeyManager.UserKeys)
                .then((results) => {
                    resolve(results);
                }).catch(async () => {
                    const apiName = 'keys';
                    const path = '/keys';

                    const myInit = {
                        headers: {},
                        response: true,
                    };
                    const res = await API.get(apiName, path, myInit);
                    await BrowserStorage.setKey(StorageKeys.KeyManager.UserKeys, res.data);
                    resolve(res.data);
                });
        });
    }

    static async setMasterPassword(password) {
        const ec = new TextEncoder();
        const userKeys = await this.getUserKeys();
        const KEK = await Crypto.generateKEK(ec.encode(password), b64.decode(userKeys.salt));
        const DEK = await Crypto.generateDEK();
        const EDEK = await Crypto.encrypt(DEK, KEK, b64.decode(userKeys.iv));

        return this.commitEDEK(DEK, EDEK);
    }

    static async changeMasterPassword(oldPassword, newPassword) {
        const userKeys = await this.getUserKeys();
        const ec = new TextEncoder();

        // New Key Encryption Key
        const KEK = await Crypto.generateKEK(ec.encode(newPassword), b64.decode(userKeys.salt));

        try {
            // If we have a decrypted key in storage we can encrypt it directly with the new KEK and upload it
            const DEK = await this.getDEK();
            const newEDEK = await Crypto.encrypt(b64.decode(DEK), KEK, b64.decode(userKeys.iv));
            await this.commitEDEK(b64.decode(DEK), newEDEK);
        } catch (e) {
            // If we don't have a DEK, we need to get it from EDEK
            if (userKeys.edek) {
                const oldKEK = await Crypto.generateKEK(ec.encode(oldPassword), b64.decode(userKeys.salt));
                const DEK = await Crypto.decrypt(b64.decode(userKeys.edek), oldKEK, b64.decode(userKeys.iv));
                const newEDEK = await Crypto.encrypt(DEK, KEK, b64.decode(userKeys.iv));
                await this.commitEDEK(DEK, newEDEK);
            } else {
                // If there is absolutely nothing available, just set the newPassword as master password.
                try {
                    await this.setMasterPassword(newPassword);
                } catch(e) {
                    Tools.debugLog("Could not change user encryption keys.");
                    Tools.debugLog(e);
                }
            }
        }
        
    }

    static async commitEDEK(DEK, EDEK) {
        const userKeys = await this.getUserKeys();

        // Commit EDEK to database + locally
        return new Promise(async (resolve, reject) => {
            const apiName = 'keys';
            const path = '/keys';

            const myInit = {
                headers: {},
                body: {
                    EDEK: EDEK.ciphertext
                }
            };
            await API.post(apiName, path, myInit);

            await BrowserStorage.setKey(StorageKeys.KeyManager.UserKeys, {
                ...userKeys,
                edek: EDEK.ciphertext
            });

            await this.saveDEK(b64.encode(DEK));

            resolve(EDEK.ciphertext);
        });
    }
}