import BrowserStorage from "../BackgroundTools/BrowserStorage";
import { StorageKeys } from "../BackgroundTools/StorageKeys";

import './WhitelistMessages';

export class Whitelist {
    static async hostnameIsBlocked(hostname) {
        try {
            const disabledHostnames = await BrowserStorage.getKey(StorageKeys.Whitelist.Whitelist);
            return disabledHostnames.includes(hostname);
        } catch {
            return false;
        }
    } 

    static async updateHostnameState(hostname, state) {
        try {
            let disabledHostnames = await this.getDisabledHostnames();

            // If state is true, remove hostname from disabledHostnames
            if (state) {
                disabledHostnames =
                    disabledHostnames.filter(disabledHostname => disabledHostname !== hostname);
            } else {
                disabledHostnames.push(hostname);
            }

            await this.saveDisabledHostnames(disabledHostnames);
        } catch(e) {
            throw new Error(e);
        }
    }

    static async getDisabledHostnames() {
        try {
            const disabledHostnames = await BrowserStorage.getKey(StorageKeys.Whitelist.Whitelist);
            return disabledHostnames;
        } catch(e) {
            return [];
        }
    }

    static async saveDisabledHostnames(hostnames) {
        try {
            await BrowserStorage.setKey(StorageKeys.Whitelist.Whitelist, hostnames);
        } catch(e) {
            throw new Error(e);
        }
    }
}