import './EntitlementMessage';

import BrowserStorage from '../BackgroundTools/BrowserStorage';
import { StorageKeys } from '../BackgroundTools/StorageKeys';
import BrowserHelper from '../../shared/BrowserHelper';

import { EntitlementConstants } from './Constants';
import { SARefreshable } from '../SARefreshable/SARefreshable';
import { BackgroundTools } from '../BackgroundTools/BackgroundTools';

export class Entitlement {

    static getTodayString() {
      return new Date().setHours(0, 0, 0, 0);
    }

    static async getUserEntitlement() {
      // User is always entitled if this is not Safari on iOS
      if (!BrowserHelper.isMobileSafari(navigator.userAgent)) {
        return true;  
      }

      try {
        await SARefreshable.getUserEntitlement();
      } catch(e) {
        console.log("Couldn't refresh entitlement.");
      }

      try {
        const hasEntitlement = await BrowserStorage.getKey(StorageKeys.Entitlement.HasEntitlement, false);
        // Immediately return true if user is entitled. 
        if (hasEntitlement.hasEntitlement) {
          return true;
        } 

        const numberOfRuns = await this.getCounter();

        if (numberOfRuns >= EntitlementConstants.FREE_DAILY) {
          await BackgroundTools.showSubscribe();
          return false;
        }

        return true;
      } catch(e) {
        return true;
      }
    }

    static async getCounter() {
      try {
        const result = await BrowserStorage.getKey(StorageKeys.Entitlement.NumberOfRuns + this.getTodayString());
        return result.numberOfRuns;
      } catch(e) {
        return 0;
      }
    }

    static async incrementCounter({ hostname }) {
      if (hostname === 'dev.super-agent.com' || hostname === 'account.super-agent.com') {
        return;
      }
      // Check if we have a key already
      // If not, delete everything with the prefix and create for today
      try {
        const result = await BrowserStorage.getKey(StorageKeys.Entitlement.NumberOfRuns + this.getTodayString());
        let numberOfRuns = result.numberOfRuns;

        await BrowserStorage.setKey(StorageKeys.Entitlement.NumberOfRuns + this.getTodayString(), {
          numberOfRuns: numberOfRuns + 1
        });

        return;
      } catch (e) {
        await BrowserStorage.clearAllWithPrefix(StorageKeys.Entitlement.NumberOfRuns);
        await BrowserStorage.setKey(StorageKeys.Entitlement.NumberOfRuns + this.getTodayString(), {
          numberOfRuns: 1
        });
        return;
      }
    }
    
    static async setUserEntitlement(hasEntitlement) {
      try {
        await BrowserStorage.setKey(StorageKeys.Entitlement.HasEntitlement, {
          hasEntitlement: hasEntitlement
        });
      } catch(e) {
        
      }
    }

    static async setUserIsRCIdSynced(isRCIdSynced) {
      try {
        await BrowserStorage.setKey(StorageKeys.Entitlement.IsRCIdSynced, {
          isRCIdSynced: isRCIdSynced
        })
      } catch (e) {

      }
    }

    static async getCurrentEntitlement() {
      // User is always entitled if this is not Safari on iOS
      if (!BrowserHelper.isMobileSafari(navigator.userAgent)) {
        return true;
      }

      try {
        await SARefreshable.getUserEntitlement();
      } catch(e) {
        console.log("Couldn't refresh entitlement.");
      }

      try {
        const hasEntitlement = await BrowserStorage.getKey(StorageKeys.Entitlement.HasEntitlement, false);
        // Immediately return true if user is entitled.
        return hasEntitlement.hasEntitlement
      } catch(e) {
        return true;
      }
    }

  static async getIsRCIdSynced() {
    // User is always entitled if this is not Safari on iOS
    if (!BrowserHelper.isMobileSafari(navigator.userAgent)) {
      return true;
    }

    try {
      const isRCIdSynced = await BrowserStorage.getKey(StorageKeys.Entitlement.IsRCIdSynced, false);
      // Immediately return true if user is entitled.
      return isRCIdSynced.isRCIdSynced
    } catch(e) {
      return true;
    }
  }

  static async getRCIdInstalledAt() {
    if (!BrowserHelper.isMobileSafari(navigator.userAgent)) {
      return null;
    }

    try {
      const RCIdInstalledAt = await BrowserStorage.getKey(StorageKeys.Entitlement.InstalledAt, false);
      return RCIdInstalledAt.installedAt
    } catch(e) {
      return null;
    }
  }

  static async setRCIdExpiration({ hasEntitlement, isRCIdSynced }){
    if (!BrowserHelper.isMobileSafari(navigator.userAgent)) {
      return
    }

    if (this.isSubscribedButNotSynced({ hasEntitlement, isRCIdSynced })) {
      try {
        const installedAt = await Entitlement.getRCIdInstalledAt()
        if (installedAt) {
          return
        }
        await this.setInstalledAt()
      } catch (e) {

      }
    }
  }

  static isSubscribedButNotSynced({ hasEntitlement, isRCIdSynced }) {
    return hasEntitlement && !isRCIdSynced
  }

  static async setInstalledAt() {
    await BrowserStorage.setKey(StorageKeys.Entitlement.InstalledAt, { installedAt: this.getTodayString() })
  }
}

export class SubscriptionVerificationNotification {
  static async setPopupShownAt() {
    await BrowserStorage.setKey(StorageKeys.Entitlement.PopupShownAt, { popupShownAt: Entitlement.getTodayString() })
  }

  static async getPopupShownAt() {
    try {
      const popupShownAt = await BrowserStorage.getKey(StorageKeys.Entitlement.PopupShownAt, false);
      return popupShownAt.popupShownAt || null;
    } catch (e) {
      return null;
    }
  }
}
