import { API, graphqlOperation } from 'aws-amplify';

import BrowserStorage from '../BackgroundTools/BrowserStorage';
import { StorageKeys } from '../BackgroundTools/StorageKeys';

import * as queries from '../graphql/queries';
import * as mutations from '../graphql/mutations';

import { Tools } from '../../Tools/Tools';
import NativeMessaging from "../../shared/NativeMessaging";

import './UPMessage';
import { SAAnalytics } from '../Analytics/SAAnalytics';

const defaultUserPreferences = {
    advertising: false,
    performance: true,
    functional: true,
    other: false,
    background: true,
    enableFilters: true,
    analyticsOptIn: false,
    showUpdateNotifications: true
};

export class UserPreferences {

    static getUserPreferences(isLoggedIn) {
        return new Promise(async (resolve, _) => {
            BrowserStorage.getKey(StorageKeys.UserPreferences.Preferences, isLoggedIn)
                .then((results) => {
                    resolve(results.userPreferences);
                })
                .catch(async () => {
                    let res;

                    if (isLoggedIn) {
                        try {
                            res = await API.graphql(graphqlOperation(queries.getUserPreferences));

                            if (res.data.getUserPreferences.analyticsOptIn == null) {
                                res.data.getUserPreferences.analyticsOptIn = false;
                            }

                            res = res.data.getUserPreferences;

                            // Future proofing
                            for (const key in res) {
                                if (res[key] == null) {
                                    res[key] = defaultUserPreferences[key];
                                }
                            }
                        } catch (e) {
                            res = defaultUserPreferences;
                        }
                    } else {
                        // No user logged in and nothing in cache
                        res = defaultUserPreferences;
                    }

                    const data = {
                        userPreferences: res
                    }

                    await BrowserStorage.setKey(StorageKeys.UserPreferences.Preferences, data);
                    resolve(res);
                });
        });
    }

    static async refreshUserPreferences() {
        let res;

        try {
            res = await API.graphql(graphqlOperation(queries.getUserPreferences));

            if (res.data.getUserPreferences.enableFilters == null) {
                res.data.getUserPreferences.enableFilters = true;
            }

            res = res.data.getUserPreferences;

            const data = {
                userPreferences: res
            }

            await BrowserStorage.setKey(StorageKeys.UserPreferences.Preferences, data);
        } catch (e) {
            // Do nothing
        }
    }

    static async refreshUserPreferencesNotLoggedIn(isMobileSafari) {

        if (isMobileSafari) {

            try {
                let appPreferences = await NativeMessaging.sendNativeMessageIfPossibleWithResponse("CookiePreferences");
                if (appPreferences.didSetCookiePreferences === false) {

                    Tools.debugLog("didThrowError when getting CookiePreferences. DidSetCookiePreferences = false");
                    throw new Error("No app cookie preferences");

                }

                const data = {
                    userPreferences: appPreferences
                }

                await BrowserStorage.setKey(StorageKeys.UserPreferences.Preferences, data);

            } catch (e) {
                Tools.debugLog("Error getting cookie preferences from app: " + e);
            }

        }

    }

    static setUserPreferences(userPreferences, isLoggedIn) {
        return new Promise(async (resolve, _) => {
            if (isLoggedIn) {
                await API.graphql(graphqlOperation(mutations.updateUserPreferences, {
                    input: userPreferences
                }));
            }

            const data = {
                userPreferences: userPreferences
            }

            await SAAnalytics.recordSaveUserPreferences(userPreferences);

            await BrowserStorage.setKey(StorageKeys.UserPreferences.Preferences, data);
            resolve();
        });
    }
}
